.links {
  margin-bottom: 140px;

  &__wrapper {
    display: flex;
    flex-direction: column;
    gap: 60px;
  }

  &__flex {
    display: flex;
    flex-direction: column;
    gap: 40px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }

  &__item {
    padding: 10px 10px 10px 24px;
    width: calc(25% - 10px);
    border-radius: 16px;
    background-color: var(--back-main);
    transition: background-color 0.7s, color 0.7s;

    &:hover {
      color: var(--white);
      background-color: var(--main);
      transition: background-color 0.7s, color 0.7s;
    }

    & a {
      display: flex;
      gap: 20px;
      justify-content: space-between;
      align-items: center;
    }

    & span {
      display: flex;
      justify-content: center;
      align-items: center;
      min-width: 56px;
      width: 56px;
      height: 56px;
      border-radius: 12px;
      background-color: var(--white);
    }

    & span::before {
      content: "";
      width: 15px;
      height: 12px;
      mask-image: url("../assets/img/icons/stack.svg#arrow-right");
      mask-position: center;
      background-color: var(--main);
    }
  }
}

@media (max-width: 1300px) {
  .links__item {
    width: calc(50% - 10px);
  }
}

@media (max-width: 768px) {
  .links {
    margin-bottom: 70px;

    &__wrapper {
      gap: 30px;
    }

    &__flex {
      gap: 20px;
    }

    &__list {
      flex-direction: column;
    }

    &__item {
      padding: 6px 6px 6px 20px;
      width: 100%;
      border-radius: 12px;

      & span {
        min-width: 52px;
        width: 52px;
        height: 52px;
      }
    }
  }
}