.simptoms {
  &__wrapper {
    position: relative;
  }

  &__title {
    margin-bottom: 35px;
  }

  &__list {
    display: flex;
    gap: 10px;
  }

  &__phone {
    position: absolute;
    top: 14px;
    right: 0;
    display: flex;
    gap: 16px;
    width: 360px;

    &::before {
      content: "";
      width: 20px;
      height: 20px;
      background-image: url("../assets/img/icons/stack.svg#phone");
    }
  }
}

.simptom {
  padding: 26px 26px 40px 40px;
  width: calc(33% - 5px);
  height: auto;
  display: flex;
  flex-direction: column;
  border-radius: 32px;
  background-color: #ddcabe33;

  &--accent {
    background-color: #bc33331a;
  }

  &__name {
    margin-bottom: 35px;
  }

  & ul {
    margin-top: 24px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    max-height: 240px;
    overflow: hidden;
    transition: max-height 0.7s;
  }
}

@media (max-width: 1023px) {
  .simptoms {
    &__title {
      margin-bottom: 24px;
      text-align: left;
    }

    &__list {
      flex-direction: column;
    }

    &__phone {
      position: static;
      margin-top: 30px;
      width: 100%;

      &::before {
        content: "";
        width: 16px;
        height: 16px;
        background-size: 16px 16px;
      }
    }
  }

  .simptom {
    padding: 24px 20px;
    width: 100%;
    border-radius: 16px;

    &__name {
      margin-bottom: 25px;
    }

    & ul {
      margin-top: 20px;
      gap: 10px;
    }
  }
}