// footer

.footer {
  margin-top: auto;
  padding: 120px 0;
  border-radius: 40px;
  color: var(--white);
  background-color: var(--black);

  & p,
  & a {
    opacity: 0.8;
  }

  &__wrapper {
    padding-bottom: 104px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid var(--border);
  }

  &__left {
    width: 25%;
    display: flex;
    flex-direction: column;
    gap: 55px;
  }

  & .logo {
    margin-bottom: 32px;
    max-width: 100%;
  }

  & .logo__text {
    opacity: 1;
    font-size: 24px;
    color: var(--white);
  }

  p.footer__title {
    margin-bottom: 24px;
    font-weight: 600;
    font-size: 20px;
    opacity: 1;
  }

  & .search__input {
    color: #ffffff;
    background-color: #ffffff1a;
  }

  & .search__input::placeholder {
    color: #ffffff;
  }

  &__right {
    width: 61%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: space-between;
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }

  &__nav a:hover {
    color: var(--main);
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  &__bottom {
    padding-top: 80px;
    font-size: 14px;
  }

  &__links {
    margin-bottom: 30px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }

  &__copy {
    display: flex;
    justify-content: space-between;
  }

  &__blind button {
    display: flex;
    gap: 12px;

    &::before {
      content: "";
      margin-top: 3px;
      width: 20px;
      height: 17px;
      background-image: url("../assets/img/icons/stack.svg#eye");
    }
  }
}

.pay {
  width: 100%;
}

.pay__list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.pay__item {
  padding: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  height: 113px;
  border-radius: 20px;

  & svg {
    fill: var(--white);
  }
}

.footer__pay {
  .pay__item {
    width: 181px;
    background-color: #ffffff1a;
  }
}

.rating {
  display: flex;
  gap: 6px;

  & span {
    display: block;
    min-width: 16px;
    width: 16px;
    height: 16px;

    &.active {
      background-image: url("../assets/img/icons/stack.svg#star");
    }
  }
}

@media (max-width: 870px) {
  .footer {
    padding: 50px 0;
    border-radius: 20px;
    font-size: 14px;

    &__wrapper {
      padding-bottom: 41px;
      flex-direction: column;
      gap: 40px;
    }

    &__left {
      width: 100%;
      gap: 40px;
    }

    & .logo {
      margin-bottom: 24px;
      max-width: 100%;
      width: 100%;
    }

    & .logo__text {
      opacity: 1;
      font-size: 24px;
      color: var(--white);
    }

    p.footer__title {
      margin-bottom: 20px;
      font-size: 18px;
    }

    &__right {
      width: 100%;
      gap: 40px;
      justify-content: flex-start;
    }

    &__nav {
      flex-direction: column;
      gap: 40px;
    }

    &__nav a:hover {
      color: var(--main);
    }

    &__list {
      display: flex;
      flex-direction: column;
      gap: 12px;
    }

    &__bottom {
      padding-top: 40px;
    }

    &__links {
      flex-direction: column;
      gap: 30px;
    }

    &__copy {
      flex-direction: column;
      gap: 30px;
    }
  }

  .pay__list {
    flex-direction: column;
  }

  .footer__pay .pay__item {
    padding: 19px 20px;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    width: 100%;
    height: 54px;
    border-radius: 12px;

    & svg {
      width: 20px;
      height: auto;
    }

    & img {
      width: 20px;
      height: auto;
    }
  }

  .rating {
    & span {
      min-width: 14px;
      width: 14px;
      height: 14px;

      &.active {
        background-size: 14px 14px;
      }
    }
  }
}

