// 404

.not-found {
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;

  &__wrapper {
    display: flex;
    gap: 10px;
  }

  &__text {
    padding: 70px 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 40px;
    background-color: var(--back-accent);
  }

  &__404 {
    margin-bottom: 20px;
    font-size: 200px;
    line-height: 80%;
    font-weight: 600;
    color: var(--main);
  }

  &__buttons {
    display: flex;
    gap: 10px;
  }

  &__accent {
    margin: 50px 0;
    padding: 40px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    border-radius: 24px;
    background-color: var(--white);

    & p {
      opacity: 0.8;
    }
  }

  &__doctor {
    min-width: calc(50% - 10px);
    border-radius: 40px;
    background-color: var(--main);
    overflow: hidden;

    & picture {
      width: 100%;
      height: 100%;
    }

    & img {
      margin: 0 auto;
      height: 100%;
      width: auto;
      object-fit: cover;
    }
  }

  &__links {
    .links__title {
      text-align: center;
    }

    .links__item {
      background-color: var(--white);

      &:hover {
        background-color: var(--main);
      }
    }

    .links__item span {
      background-color: var(--back-main);
    }
  }
}

@media (max-width: 1300px) {
  .not-found__wrapper {
    flex-direction: column;
  }
}

@media (max-width: 1023px) {
  .not-found {
    padding: 0;

    &__text {
      padding: 40px 20px;
      border-radius: 20px;
    }

    &__404 {
      font-size: 150px;
    }

    &__buttons {
      flex-direction: column;
      width: 100%;

      .button {
        width: 100%;
      }
    }

    &__accent {
      margin: 30px 0;
      padding: 24px 20px;
      gap: 16px;
      border-radius: 16px;
    }

    &__doctor {
      min-width: 100%;
      border-radius: 14px;

      & img {
        margin: 0 auto;
        height: auto;
        max-height: 400px;
        width: 100%;
        object-fit: contain;
      }
    }

    &__links {
      .links__title {
        text-align: center;
      }

      .links__item {
        background-color: var(--white);

        &:hover {
          background-color: var(--main);
        }
      }

      .links__item span {
        background-color: var(--back-main);
      }
    }
  }
}