.calculator {
  &__title {
    margin-bottom: 24px;
    text-align: center;
  }

  & .lead {
    text-align: center;
  }

  &__wrapper {
    margin: 0 auto;
    padding: 50px;
    display: flex;
    flex-direction: column;
    gap: 24px;
    width: 900px;
    border-radius: 32px;
    background-color: var(--white);
  }

  &__navigation {
    display: flex;
    align-items: center;

    &--mobile {
      display: none;
    }

    & p {
      margin-right: auto;
      opacity: 0.7;
    }
  }

  &__navigation.hidden {
    display: none;
  }
}

.answers {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.answer {
  position: relative;
  padding: 30px;
  border-radius: 16px;
  border: 1px solid var(--border);

  &__label {
    display: flex;
    gap: 20px;
    cursor: pointer;
  }

  &__title {
    margin-bottom: 10px;
    display: block;
    font-size: 18px;
    font-weight: 600;
  }
}

.slide {
  display: none;
  animation: fadeIn 0.5s ease;

  &__question {
    padding: 0 100px;
    text-align: center;
  }
}

.slide.active {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.calculator__result {
  display: none;
}

.calculator__result.active {
  display: flex;
  flex-direction: column;
  gap: 25px;

  & .title-small {
    text-align: center;
  }
}

.result__header {
  text-align: center;
}

.result__title {
  margin-bottom: 20px;
}

.result__subtitle {
  margin-bottom: 24px;
  font-size: 20px;
  font-weight: 600;
  color: var(--accent);
}

.result__match {
  margin: 0 auto;
  padding: 8px 20px;
  width: max-content;
  font-size: 20px;
  font-weight: 600;
  color: var(--main);
  border-radius: 25px;
  border: 1px solid var(--main);
  background-color: var(--back-main);
}

.recommendation__title {
  margin-bottom: 24px;
}

.recommendation__list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

@media (max-width: 1023px) {
  .inner-page .calculator {
    padding: 50px 20px;
    border-radius: 20px;
    background-color: var(--back-accent);
  }
}

@media (max-width: 900px) {
  .calculator {
    &__title {
      margin-bottom: 20px;
      text-align: left;
    }

    & .lead {
      text-align: left;
    }

    &__wrapper {
      padding: 24px 20px;
      width: 100%;
      border-radius: 16px;
    }

    &__navigation {

      &--mobile {
        display: flex;
      }

      &--desktop {
        display: none;
      }
    }
  }

  .answer {
    padding: 20px;
    border-radius: 12px;

    &__label {
      gap: 14px;

      & span {
        font-size: 16px;
      }
    }
  }

  .slide {
    &__question {
      padding: 0;
      text-align: left;
    }
  }

  .calculator__result.active {
    gap: 20px;
  }

  .result__title {
    margin-bottom: 20px;
  }

  .result__subtitle {
    margin-bottom: 20px;
    font-size: 18px;
  }

  .result__match {
    font-size: 18px;
  }

  .recommendation__title {
    margin-bottom: 20px;
  }

  .recommendation__list {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
}