// features

.features {
  position: relative;
  background-image: url("../assets/img/sections/back-circles.png");
  background-position: top center;
  background-repeat: no-repeat;

  &__wrapper {
    padding: 92px 0 60px 0;
    display: flex;
    flex-direction: column;
    gap: 58px;
  }

  &__center {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translateX(-50%);
    width: 558px;
    text-align: center;
  }

  &__title {
    margin-bottom: 40px;
  }

  &__list {
    display: flex;
    justify-content: center;
    gap: 100px;

    &--middle {
      justify-content: space-between;
    }

    &--top {
      & .features__item:nth-child(2) {
        margin-top: -32px;
      }
    }

    &--bottom {
      & .features__item:nth-child(2) {
        margin-top: 32px;
      }
    }
  }
}

.feature {
  padding: 24px;
  border-radius: 24px;
  width: 350px;
  height: 200px;
  background-color: var(--white);
  display: flex;
  flex-direction: column;

  &__wrapper {
    margin-bottom: auto;
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }

  &__name {
    color: var(--main);
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 54px;
    min-width: 54px;
    height: 54px;
    border-radius: 12px;
    background-color: var(--main);
  }
}

@media (max-width: 1260px) {
  .feature {
    width: 300px;
  }
  .features__center {
    width: 358px;
  }
}

@media (max-width: 1023px) {
  .features {
    background-image: none;

    &__wrapper {
      padding: 50px 0;
      flex-direction: column;
      gap: 8px;
    }

    &__center {
      position: static;
      transform: none;
      width: 100%;
      text-align: left;
    }

    &__title {
      margin-bottom: 20px;
    }

    &__list {
      flex-direction: column;
      gap: 8px;

      &--middle {
        justify-content: space-between;
      }

      &--top {
        & .features__item:nth-child(2) {
          margin-top: 0;
        }
      }

      &--bottom {
        & .features__item:nth-child(2) {
          margin-top: 0;
        }
      }
    }
  }

  .feature {
    padding: 14px;
    border-radius: 16px;
    width: 100%;
    height: 149px;

    &__icon {
      width: 46px;
      min-width: 46px;
      height: 46px;
      border-radius: 10px;

      & svg {
        width: 18px;
        height: 20px;
      }
    }

    &__description {
      padding-right: 30px;
    }
  }
}