.banner {
  border-radius: 40px;
  color: var(--white);
  background: var(--back-grad);

  &__wrapper {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }

  &__left {
    display: flex;
    flex-direction: column;
    gap: 25px;
    width: 40%;
  }

  &__description {
    padding-right: 150px;
  }

  &__phone {
    margin: auto auto 0 0;
    padding: 24px 41px;
    display: flex;
    align-items: center;
    gap: 20px;
    border-radius: 40px;
    background-color: #ffffff1a;

    &::before {
      content: "";
      width: 20px;
      height: 20px;
      background-image: url("../assets/img/icons/stack.svg#phone");
    }
  }

  &__form {
    padding: 71px 70px;
    display: flex;
    gap: 20px 12px;
    flex-wrap: wrap;
    width: 792px;
    color: var(--black);
    border-radius: 32px;
    background-color: var(--back-accent);
  }

  & input[type="checkbox"] {
    border-color: var(--main);
  }

  & input[type="checkbox"]:checked::before {
    background-image: url("../assets/img/icons/stack.svg#checkbox-green");
  }
}

@media (max-width: 768px) {
  .banner {
    border-radius: 20px;

    &__wrapper {
      flex-direction: column;
    }

    &__left {
      gap: 20px;
      width: 100%;
    }

    &__description {
      padding-right: 0;
    }

    &__phone {
      margin: 0;
      padding: 18px;
      display: flex;
      justify-content: center;

      &::before {
        content: "";
        width: 16px;
        height: 16px;
        background-size: 16px 16px;
      }
    }

    &__form {
      padding: 24px 20px;
      display: flex;
      gap: 8px;
      border-radius: 16px;
      width: 100%;
    }
  }
}