// reviews

.reviews {
  &__wrapper {
    position: relative;
  }

  &__title {
    margin-bottom: 30px;
  }

  &__buttons {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    gap: 30px;
  }

  &__slider {
    overflow: hidden;
  }

  &__link {
    width: 320px;
  }
}

.review {
  padding: 40px;
  display: flex;
  flex-direction: column;
  height: auto;
  border-radius: 32px;
  background-color: var(--back-main);

  &__top {
    margin-bottom: 40px;
    display: flex;
    gap: 20px;
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: var(--main);
  }

  &__info {
    margin-right: auto;
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__date {
    color: var(--main);
  }

  &__name {
    font-weight: 600;
    font-size: 20px;
  }

  &__text {
    max-height: 142px;
    overflow: hidden;
    transition: max-height 0.4s ease-out;
  }

  &__button {
    margin-top: 24px;
    text-align: left;
    color: var(--main);

    &:hover {
      opacity: 0.8;
    }
  }

  &__button.hidden {
    display: none;
  }
}

@media (max-width: 768px) {
  .reviews {
    &__title {
      margin-bottom: 20px;
    }

    &__buttons {
      position: static;
      margin-top: 30px;
    }

    &__link {
      width: 100%;
    }
  }

  .review {
    padding: 20px 20px 24px 20px;
    border-radius: 16px;

    &__top {
      margin-bottom: 20px;
      gap: 12px;
    }

    &__icon {
      width: 40px;
      height: 40px;

      & svg {
        width: 20px;
        height: 20px;
      }
    }

    &__name {
      margin-bottom: 10px;
      font-size: 16px;
    }

    &__text {
      max-height: 320px;
    }

    &__button {
      margin-top: 20px;
    }
  }
}

.reviews-page {
  margin-bottom: 140px;

  .reviews__list {
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }

  .review {
    display: none;
    width: calc(50% - 5px);
  }

  .review.active {
    display: flex;
  }

  .button--more {
    margin-top: 50px;
  }
}

@media (max-width: 768px) {

  .reviews-page {
    .reviews__list {
      margin-top: 30px;
    }
    
    .review {
      width: 100%;
    }
  }
}