// articles

.articles {
  margin-bottom: 110px;

  &__list {
    margin-bottom: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 10px;
  }
}

.article {
  position: relative;
  display: none;
  padding: 16px;
  height: 100%;
  border-radius: 32px;
  background-color: var(--back-main);

  &.active {
    display: flex;
  }

  & a {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  &__top {
    padding: 24px 24px 20px 24px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    min-height: 204px;
    border-radius: 26px;
    color: var(--white);
    background-color: var(--accent);
  }

  &__topic {
    margin-right: auto;
    padding: 10px 16px;
    background-color: #ffffff33;
    border-radius: 40px;
  }

  &__title {
    margin-top: auto;
  }

  &__bottom {
    padding: 32px 24px 24px 24px;
    display: flex;
    flex-direction: column;
    gap: 32px;
    flex-grow: 1;
  }

  &__link {
    margin-top: auto;
  }
}

@media (min-width: 1023px) {
  .article__date {
    position: absolute;
    top: 40px;
    right: 40px;
    padding: 10px 16px 8px 16px;
    border-radius: 40px;
    opacity: 0.8;
    color: var(--white);
    border: 1px solid #ffffff73;

    & p {
      display: none;
    }
  }
}

@media (max-width: 1023px) {
  .articles__list {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 768px) {
  .articles {
    &__list {
      display: flex;
      flex-direction: column;
      gap: 10px;
    }
  }

  .article {
    padding: 10px;
    border-radius: 16px;

    &__top {
      padding: 16px;
      gap: 17px;
      min-height: 160px;
      border-radius: 20px;
    }

    &__topic {
      font-size: 14px;
    }

    &__bottom {
      padding: 20px 10px 10px 10px;
      display: flex;
      flex-direction: column;
      gap: 20px;
      flex-grow: 1;
      font-size: 14px;
    }

    &__date {
      padding-top: 20px;
      display: flex;
      justify-content: space-between;
      border-top: 1px solid var(--border);

      & p {
        opacity: 0.6;
      }
    }

    &__link {
      margin-top: 0;
    }
  }
}

.intro__notes {
  margin-bottom: 40px;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.intro__note {
  padding: 10px 18px;
  border-radius: 40px;
  display: flex;
  align-items: center;
  gap: 10px;
  border: 1px solid var(--border);

  &--accent {
    color: var(--white);
    background-color: var(--accent);
    border: none;
  }

  &--time::before {
    content: "";
    width: 16px;
    height: 16px;
    background-image: url("../assets/img/icons/stack.svg#time");
  }

  &--date::before {
    content: "";
    width: 16px;
    height: 16px;
    background-image: url("../assets/img/icons/stack.svg#date");
  }
}

.article-page {
  margin-top: 70px;

  &__wrapper {
    margin-bottom: 140px;
    display: flex;
    gap: 100px;
  }

  &__sidebar {
    min-width: 27%;
    display: flex;
    flex-direction: column;
    gap: 20px;

    & ul {
      margin-top: 40px;
      display: flex;
      flex-direction: column;
      gap: 24px;

      & li:not(:last-child) {
        padding-bottom: 24px;
        border-bottom: 1px dashed var(--border);
      }

      & a:hover {
        color: var(--accent);
        transition: color 0.7s;
      }
    }
  }

  &__recomendation {
    padding: 40px;
    border-radius: 32px;
    background-color: var(--back-accent);

    & .title-small {
      margin-bottom: 24px;
    }

    & ul {
      margin: 0;
    }
  }

  &__banner {
    padding: 40px;
    display: flex;
    flex-direction: column;
    gap: 24px;
    border-radius: 32px;
    height: 330px;
    color: var(--white);
    background-color: var(--accent);
  }

  &__button {
    margin-top: auto;
    color: var(--black);
    background-color: var(--white);

    & span {
      background-color: var(--accent);

      &::before {
        background-color: var(--white);
      }
    }

    &:hover {
      background-color: var(--back-accent);

      & span::before {
        background-color: var(--back-accent);
      }
    }
  }

  .check {
    flex-direction: column;
  }

  .check-doctor {
    width: 100%;
  }

  .quote {
    margin: 35px 0;
    padding: 32px 25px;
    border-radius: 24px;
    color: var(--white);
    background-color: var(--main);

    &__bottom {
      padding-top: 30px;
      border-top: 1px solid #ffffff33;
      display: flex;
      gap: 20px;

      &::after {
        content: "";
        margin-left: auto;
        min-width: 24px;
        height: 20px;
        background-image: url("../assets/img/icons/stack.svg#quote");
      }
    }

    &__img {
      width: 92px;
      min-width: 92px;
      height: 92px;
      border-radius: 16px;
      background: #ffffff33;
      overflow: hidden;

      & img {
        width: 100%;
        height: 100%;
        border-radius: 0;
        object-fit: cover;
      }
    }

    &__text {
      display: flex;
      flex-direction: column;
      width: 100%;
    }

    &__name {
      margin-bottom: 20px;
      font-size: 20px;
      font-weight: 600;
    }

    &__work {
      margin-top: auto;
    }
  }
}

@media (max-width: 1023px) {
  .article-page {
    margin-top: 30px;
    padding: 50px 0;
    border-radius: 20px;
    background-color: var(--back-accent);

    &__wrapper {
      margin-bottom: 20px;
      flex-direction: column-reverse;
      gap: 40px;
    }

    &__sidebar {
      min-width: 100%;
    }

    &__recomendation {
      padding: 24px 20px;
      border-radius: 16px;
      background-color: var(--white);

      & .title-small {
        margin-bottom: 24px;
      }

      & ul {
        gap: 20px;

        & li:not(:last-child) {
          padding-bottom: 20px;
        }
      }
    }

    &__banner {
      display: none;
    }

    .quote {
      padding: 24px 20px;
      border-radius: 16px;

      & p {
        margin-bottom: 0;
      }

      &__bottom {
        display: none;
      }
    }
  }

  .text-page .article-page__wrapper {
    flex-direction: column;
  }

  .text-page .article-page__recomendation {
    background-color: var(--back-accent);
  }
}