.scroll-top {
  width: 60px;
  height: 60px;
  position: fixed;
  right: 40px;
  bottom: 200px;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 1px solid var(--main);
  background-color: var(--white);
  opacity: 0;
  transition: opacity 0.7s;
  cursor: pointer;
}

@media (max-width: 768px) {
  .scroll-top {
    right: 20px;
    bottom: 70px;
    width: 40px;
    height: 40px;

    & svg {
      width: auto;
      height: 14px;
    }
  }
}

// popups

.popup {
  height: 100vh;
  left: 50%;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
  width: 100%;
  z-index: 1000;
  -webkit-transition: opacity 0.4s ease;
  transition: opacity 0.4s ease;
  overflow-y: auto;
  background: #2527347a;
  backdrop-filter: blur(14px);
}

.popup__body {
  width: 100%;
  min-height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  padding: 0 12px;
}

.popup.open {
  opacity: 1;
  pointer-events: all;
}

.popup__content {
  width: 100%;
  max-width: 772px;
  position: relative;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  padding: 60px;
  color: var(--white);
  background: var(--back-grad);
  border-radius: 32px;
}

.popup--call .input {
  color: var(--white);
  border-color: var(--white);
}

.popup--call .input::placeholder {
  color: var(--white);
}

.popup .button {
  color: var(--main);
  background-color: var(--white);
}

.popup__title {
  margin-bottom: 30px;
  font-size: 36px;
  font-weight: 600;
  text-align: center;
  line-height: 120%;
}

.popup .lead {
  padding: 0 100px;
  text-align: center;
}

.popup__close {
  cursor: pointer;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 20px;
  right: 20px;
  z-index: 1000;
  width: 40px;
  height: 40px;

  svg {
    stroke: var(--white);
    transition: transform 0.7s;
  }

  &:hover {
    svg {
      transform: rotate(360deg);
    }
  }
}

.popup-responce .popup__content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  height: 347px;
}

.popup--city {
  & .popup__content {
    max-width: 900px;
    height: 505px;
    color: var(--black);
    background: var(--white);
  }

  & .popup__wrapper {
    padding: 0 120px;
  }

  & .search__input {
    background-color: var(--back-main);
    border: none;
  }

  .search__list {
    margin-top: 24px;
    height: 300px;
    overflow-y: scroll;
  }

  .popup__cities {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    font-size: 17px;
  }

  .popup__subtitle {
    margin-top: auto;
    margin-bottom: 30px;
    text-align: center;
    font-size: 20px;
    font-weight: 600;
  }

  .popup__cities.hidden {
    display: none;
  }

  .popup__close svg {
    stroke: var(--main);
  }
}

@media (max-width: 768px) {
  .popup__content {
    padding: 40px 20px;
    max-width: 100%;
    max-height: auto;
    border-top-right-radius: 40px;
    border-bottom-left-radius: 40px;
  }

  .popup__title {
    margin-bottom: 20px;
    font-size: 24px;
    text-align: left;
  }

  .popup .lead {
    padding: 0;
    text-align: left;
  }

  .popup__close {
    top: 20px;
    right: 20px;
  }

  .popup__close {
    width: 24px;
    height: 24px;
    top: 20px;
    right: 20px;
  }

  .popup--city {
    & .popup__content {
      max-width: 100%;
      height: auto;
    }

    & .popup__wrapper {
      padding: 0;
    }

    .search__list {
      margin-top: 24px;
      height: 300px;
      overflow-y: scroll;
    }

    .popup__cities {
      display: flex;
      flex-direction: column;
      gap: 21px;
      font-size: 16px;
      height: 320px;
      overflow-y: auto;
    }

    .popup__subtitle {
      margin-top: 40px;
      margin-bottom: 20px;
      text-align: left;
      font-size: 16px;
      font-weight: 600;
    }
  }
}

.cookie-block {
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background: var(--main);
  color: var(--white);
  padding: 10px 14px;
  font-size: 14px;
  z-index: 10151;
}

.cookie-block button {
  border: 0;
  outline: 0;
  padding: 16px;
  margin: 0 8px;
  min-width: 160px;
  height: 50px;
  color: var(--main);
  background-color: var(--white);
  border-radius: 30px;
  cursor: pointer;
}

.cookie-block.accepted {
  bottom: -999px;
  opacity: .2;
}

@keyframes pulse-ring {
  0% {
    box-shadow: 0 0 0 0 rgba(116, 148, 122, 0.5);
  }

  70% {
    box-shadow: 0 0 0 10px rgba(116, 148, 122, 0.5);
  }

  100% {
    box-shadow: 0 0 0 0 rgba(116, 148, 122, 0.5);
  }
}

.widget {
  position: fixed;
  right: 40px;
  bottom: 40px;
  display: flex;
  gap: 10px;
  z-index: 10;

  &__wrapper {
    display: flex;
    gap: 10px;
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.5s ease;
    width: 0;
    opacity: 0;
    pointer-events: none;
  }

  &__wrapper.active {
    width: auto;
    opacity: 1;
    pointer-events: all;
    transform: scaleX(1);
  }

  &__item {
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    flex-shrink: 0;
    width: max-content;
    border-radius: 40px;
    color: var(--white);
    background-color: var(--main);
  }

  &__open {
    position: relative;
    z-index: 2;
    padding: 22px;
    width: 60px;
    height: 60px;
    background-color: var(--main);
    border-radius: 50%;
    box-shadow: 0 0 0 0 rgba(116, 148, 122, 0.274);
    animation: pulse-ring 2s infinite;
    transition: all 0.3s;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 16px;
      height: 2px;
      border: 10px;
      background-color: var(--white);
    }

    &::after {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 2px;
      height: 16px;
      border: 10px;
      background-color: var(--white);
    }
  }

  &__open.active {
    background-color: var(--white);
    transform: rotate(405deg);
    animation: none;
    box-shadow: 0 0 1px 1px rgba(116, 148, 122, 0.411);
  }

  &__open.active::before,
  &__open.active::after {
    background-color: var(--main);
    transition: all 0.7s;
  }
}

@media(max-width: 768px) {
  .cookie-block {
    flex-direction: column;
    gap: 10px;
    align-items: start;
    font-size: 12px;
    text-align: center;
  }

  .cookie-block button {
    min-width: 100%;
  }

  .widget {
    right: 20px;
    bottom: 20px;
    flex-direction: column;
    align-items: flex-end;

    &__wrapper {
      height: 0;
      flex-direction: column;
      align-items: flex-end;
    }

    &__wrapper.active {
      height: auto;
      transform: scaleX(1);
    }

    &__item {
      padding: 15px 20px;
      box-shadow: 0 0 1px 1px rgba(37, 37, 37, 0.192);

      & svg {
        width: 15px;
        height: auto;
      }
    }

    &__open {
      padding: 18px;
      width: 40px;
      height: 40px;

      &::before {
        width: 14px;
      }

      &::after {
        height: 14px;
      }
    }
  }
}