/* intro */

.intro {
  padding: 20px;
}

.intro__wrapper {
  padding-bottom: 74px;
  display: flex;
  justify-content: space-between;
}

.intro__wrapper.intro--small {
  padding-bottom: 0;
}

.intro__text {
  padding: 40px 0 14px 0;
  display: flex;
  flex-direction: column;
  width: 45%;
}

.intro__title {
  margin-bottom: 25px;
}

.intro__title:not(:has(+ .lead)) {
  margin-bottom: 0;
}

.lead {
  margin-bottom: 50px;
}

.intro__buttons {
  margin-top: auto;
  display: flex;
  gap: 12px;
}

.intro__button {
  min-width: 300px;
  font-size: 18px;
}

.intro__picture {
  margin-right: 17px;
  border-radius: 50%;
  outline: 1px solid #74947a2f;
  outline-offset: 40px;
  overflow: hidden;

  & img {
    border-radius: 50%;
    background-color: #e9e6df;
  }
}

.intro__list {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.intro__item,
.popup__cities li,
.recommendation__list li,
.tariff li,
.simptom li {
  display: flex;
  gap: 16px;
}

.intro__item::before,
.popup__cities li::before,
.recommendation__list li::before,
.text-content li::before,
.tariff li::before,
.simptom li::before {
  content: "";
  margin-top: 3px;
  min-width: 16px;
  width: 16px;
  height: 16px;
  background-image: url("../assets/img/icons/stack.svg#list");
  background-size: 16px 16px;
}

.simptom--accent li::before {
  background-image: url("../assets/img/icons/stack.svg#list-red");
}

.text-content li::before {
  margin-right: 20px;
  display: inline-block;
}

.inner-page {
  & .intro__text {
    padding: 0;
    width: 54%;
  }

  & .button--accent:hover {
    background-color: var(--accent);
  }
}

.intro--small .intro__text {
  width: 90%;
}

.intro--small .lead {
  margin-bottom: 0;
  width: 65%;
}

.intro--small .intro__list {
  margin-top: 50px;
}

.intro--small .intro__description {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.intro--doctor .intro__text {
  width: 62%;
}

.intro__numbers {
  display: flex;
  gap: 10px;
}

.intro__number {
  position: relative;
  padding: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 10px;
  width: calc(25% - 10px);
  min-height: 126px;
  border-radius: 16px;
  background-color: var(--white);

  & p {
    font-size: 16px;
  }

  & span {
    display: block;
    padding-right: 20px;
    font-size: 24px;
    font-weight: 600;
    color: var(--main);
  }

  &::before {
    content: "";
    position: absolute;
    width: 14px;
    height: 14px;
    right: 24px;
    top: 24px;
    background-image: url("../assets/img/icons/stack.svg#list");
  }
}

.intro--reviews {
  .intro__text {
    width: 65%;
  }

  .intro__number {
    border: 1px solid var(--border);
  }
}

.intro__notes {
  margin-bottom: 40px;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.intro__note {
  padding: 10px 18px;
  border-radius: 40px;
  display: flex;
  align-items: center;
  gap: 10px;
  border: 1px solid var(--border);

  &--accent {
    color: var(--white);
    background-color: var(--accent);
    border: none;
  }

  &--time::before {
    content: "";
    width: 16px;
    height: 16px;
    background-image: url("../assets/img/icons/stack.svg#time");
  }

  &--date::before {
    content: "";
    width: 16px;
    height: 16px;
    background-image: url("../assets/img/icons/stack.svg#date");
  }

  &--light {
    background-color: var(--accent-light);
  }

  &.intro__note--light {
    border: none;
  }
}

.intro blockquote {
  background-color: var(--accent);
  max-width: 1234px;
}

.intro__doctor {
  position: relative;
  min-width: 513px;
  width: 513px;
  height: 513px;
  border-radius: 26px;
  background: radial-gradient(88.32% 88.43% at 55.62% 60.63%, #C1CDC3 0%, #73947A 100%);
  overflow: hidden;

  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  & .intro__notes {
    position: absolute;
    margin-bottom: 0;
    left: 16px;
    bottom: 20px;
  }
}

.intro:has(+ .not-found) {
  margin: 0;
}

.intro--faq {
  &.intro__wrapper {
    padding-bottom: 0;
  }

  .lead {
    margin-bottom: 0;
  }

  .intro__text {
    width: 100%;
    flex-direction: row;
    align-items: flex-end;
    gap: 50px;
  }

  .intro__search {
    min-width: 400px;
  }

  .form__label {
    width: 100%;
  }
}

.intro--middle {
  .intro__text {
    width: 90%;
  }

  .lead {
    width: 60%;
  }
}

/* breadcrumbs */
.breadcrumbs {
  margin: 60px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  position: relative;
  width: 100%;
}

.breadcrumbs li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  gap: 8px;
  font-size: 16px;
}

.breadcrumbs li a:hover {
  opacity: 0.8;
}

.breadcrumbs li::after {
  content: "—";
  display: block;
  font-size: inherit;
}

.breadcrumbs li:last-of-type {
  opacity: 0.6;
}

.breadcrumbs li:last-of-type::after {
  display: none;
}

@media (max-width: 1023px) {
  .breadcrumbs {
    margin: 0 0 30px 0;
    font-size: 14px;
  }

  .intro {
    padding: 30px 0 0 0;

    &__wrapper {
      padding-bottom: 30px;
    }
  }

  .intro.block-back-accent {
    background-color: transparent;
  }

  .intro__text {
    padding: 0;
    width: 100%;
  }

  .intro__title {
    margin-bottom: 20px;
  }

  .lead {
    margin-bottom: 30px;
  }

  .intro__buttons {
    margin-top: 40px;
    flex-direction: column;
  }

  .intro__button {
    min-width: 100%;
    font-size: 14px;
  }

  .intro__picture {
    display: none;
  }

  .intro__list {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
  }

  .intro__item,
  .popup__cities li,
  .recommendation__list li,
  .tariff li,
  .simptom li {
    gap: 15px;
    font-size: 14px;
  }

  .intro__item::before,
  .popup__cities li::before,
  .recommendation__list li::before,
  .text-content li::before,
  .tariff li::before,
  .simptom li::before {
    content: "";
    min-width: 14px;
    width: 14px;
    height: 14px;
    background-size: 14px 14px;
  }

  .inner-page .intro__text {
    width: 100%;
  }

  .intro--small .lead {
    padding-right: 0;
    width: 100%;
  }

  .intro--small .intro__list {
    margin-top: 40px;
  }

  .intro--doctor .intro__text {
    width: 100%;
  }

  .intro--doctor {
    flex-direction: column-reverse;
  }

  .intro__numbers {
    flex-direction: column;
  }

  .intro__number {
    padding: 16px 20px;
    width: 100%;
    min-height: 83px;
    border-radius: 14px;
    background-color: var(--back-main);

    & p {
      font-size: 14px;
    }

    & span {
      font-size: 18px;
    }

    &::before {
      width: 12px;
      height: 12px;
      right: 16px;
      top: 16px;
      background-size: 12px 12px;
    }
  }

  .intro__doctor {
    margin-bottom: 30px;
    position: relative;
    min-width: 100%;
    width: 100%;
    height: auto;
    border-radius: 15px;

    & img {
      margin: 0 auto;
      max-width: 500px;
    }

    & .intro__notes {
      left: 12px;
      bottom: 12px;
    }
  }

  .intro--faq {
    .intro__text {
      flex-direction: column;
      align-items: flex-start;
      gap: 24px;
    }

    .intro__search {
      min-width: 100%;
    }
  }

  .intro--middle {
    .intro__text {
      width: 100%;
    }

    .lead {
      width: 100%;
    }
  }
}