.programs {
  &__title {
    margin-bottom: 35px;
    text-align: center;
  }

  &__list {
    display: flex;
    gap: 10px;
  }
}

.program {
  padding: 30px;
  display: flex;
  flex-direction: column;
  width: calc(33% - 10px);
  border-radius: 24px;
  background-color: var(--white);

  &__top {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__number {
    padding: 10px 20px;
    border-radius: 40px;
    font-size: 16px;
    color: var(--white);
    background-color: var(--accent);
  }

  & ul {
    margin: 32px 0;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;

    & li {
      padding: 10px 18px;
      border-radius: 40px;
      font-size: 16px;
      background-color: var(--back-main);
    }
  }

  &__cost {
    margin-top: auto;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;

    & span {
      font-size: 20px;
      font-weight: 600;
      color: var(--main);
    }
  }

  &__button {
    width: 100%;
  }
}

@media (max-width: 1300px) {
  .program {
    padding: 20px;
  }
}

@media (max-width: 1023px) {
  .programs {
    &__title {
      margin-bottom: 25px;
      text-align: left;
    }

    &__list {
      flex-direction: column;
    }
  }

  .program {
    width: 100%;
    border-radius: 16px;

    &__number {
      font-size: 14px;
    }

    & ul {
      margin: 20px 0 30px 0;
      gap: 8px;

      & li {
        padding: 10px 16px;
        font-size: 14px;
      }
    }

    &__cost {
      margin-bottom: 20px;
      display: flex;
      justify-content: space-between;

      & span {
        font-size: 14px;
      }
    }
  }
}