.cookie-block {
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background: var(--main);
  color: var(--white);
  padding: 10px 14px;
  font-size: 14px;
  z-index: 10151;
}

.cookie-block button {
  border: 0;
  outline: 0;
  padding: 16px;
  margin: 0 8px;
  min-width: 160px;
  height: 50px;
  color: var(--main);
  background-color: var(--white);
  border-radius: 30px;
  cursor: pointer;
}

.cookie-block.accepted {
  bottom: -999px;
  opacity: 0.2;
}

@keyframes pulse-ring {
  0% {
    box-shadow: 0 0 0 0 rgba(116, 148, 122, 0.5);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(116, 148, 122, 0.5);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(116, 148, 122, 0.5);
  }
}
.widget {
  position: fixed;
  right: 40px;
  bottom: 40px;
  display: flex;
  gap: 10px;
  z-index: 10;
}
.widget__wrapper {
  display: flex;
  gap: 10px;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.5s ease;
  width: 0;
  opacity: 0;
  pointer-events: none;
}
.widget__wrapper.active {
  width: auto;
  opacity: 1;
  pointer-events: all;
  transform: scaleX(1);
}
.widget__item {
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
  flex-shrink: 0;
  width: max-content;
  border-radius: 40px;
  color: var(--white);
  background-color: var(--main);
}
.widget__open {
  position: relative;
  z-index: 2;
  padding: 22px;
  width: 60px;
  height: 60px;
  background-color: var(--main);
  border-radius: 50%;
  box-shadow: 0 0 0 0 rgba(116, 148, 122, 0.274);
  animation: pulse-ring 2s infinite;
  transition: all 0.3s;
}
.widget__open::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 16px;
  height: 2px;
  border: 10px;
  background-color: var(--white);
}
.widget__open::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 2px;
  height: 16px;
  border: 10px;
  background-color: var(--white);
}
.widget__open.active {
  background-color: var(--white);
  transform: rotate(405deg);
  animation: none;
  box-shadow: 0 0 1px 1px rgba(116, 148, 122, 0.411);
}
.widget__open.active::before, .widget__open.active::after {
  background-color: var(--main);
  transition: all 0.7s;
}

@media (max-width: 768px) {
  .cookie-block {
    flex-direction: column;
    gap: 10px;
    align-items: start;
    font-size: 12px;
    text-align: center;
  }
  .cookie-block button {
    min-width: 100%;
