    margin-top: 3px;
    min-width: 16px;
    width: 16px;
    height: 16px;
    background-image: url("../assets/img/icons/stack.svg#list");
    background-size: 16px 16px;
  }
  .vacancy .button__more {
    padding: 0;
    border: none;
    justify-content: flex-start;
    font-size: 16px;
  }
  .vacancy .button__more:hover {
    color: var(--main);
    background-color: transparent;
  }
  .vacancy__salary {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--main);
    border-radius: 40px;
    border: 1px solid var(--border);
  }
  .vacancy__salary span {
    margin-left: 5px;
    display: inline-block;
    font-weight: 600;
  }
}
.not-found {
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
}
.not-found__wrapper {
  display: flex;
  gap: 10px;
}
.not-found__text {
  padding: 70px 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 40px;
  background-color: var(--back-accent);
}
.not-found__404 {
  margin-bottom: 20px;
  font-size: 200px;
  line-height: 80%;
  font-weight: 600;
  color: var(--main);
}
.not-found__buttons {
  display: flex;
  gap: 10px;
}
.not-found__accent {
  margin: 50px 0;
  padding: 40px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  border-radius: 24px;
  background-color: var(--white);
}
.not-found__accent p {
  opacity: 0.8;
}
.not-found__doctor {
  min-width: calc(50% - 10px);
  border-radius: 40px;
  background-color: var(--main);
  overflow: hidden;
}
.not-found__doctor picture {
  width: 100%;
  height: 100%;
}
.not-found__doctor img {
  margin: 0 auto;
  height: 100%;
  width: auto;
  object-fit: cover;
}
.not-found__links .links__title {
  text-align: center;
}
.not-found__links .links__item {
  background-color: var(--white);
}
.not-found__links .links__item:hover {
  background-color: var(--main);
}
.not-found__links .links__item span {
  background-color: var(--back-main);
}

@media (max-width: 1300px) {
  .not-found__wrapper {
    flex-direction: column;
  }
}
@media (max-width: 1023px) {
  .not-found {
    padding: 0;
  }
  .not-found__text {
    padding: 40px 20px;
    border-radius: 20px;
  }
  .not-found__404 {
    font-size: 150px;
  }
  .not-found__buttons {
    flex-direction: column;
    width: 100%;
  }
  .not-found__buttons .button {
    width: 100%;
  }
  .not-found__accent {
    margin: 30px 0;
    padding: 24px 20px;
    gap: 16px;
    border-radius: 16px;
  }
  .not-found__doctor {
    min-width: 100%;
    border-radius: 14px;
  }
  .not-found__doctor img {
    margin: 0 auto;
    height: auto;
    max-height: 400px;
    width: 100%;
    object-fit: contain;
  }
  .not-found__links .links__title {
    text-align: center;
  }
  .not-found__links .links__item {
    background-color: var(--white);
  }
  .not-found__links .links__item:hover {
    background-color: var(--main);
  }
  .not-found__links .links__item span {
    background-color: var(--back-main);
  }
}