.vacancies__numbers {
  display: flex;
  gap: 10px;
}

.vacancies__number {
  position: relative;
  padding: 24px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  gap: 10px;
  width: calc(25% - 10px);
  min-height: 126px;
  border-radius: 16px;
  color: var(--white);
  background-color: var(--main);

  & p {
    font-size: 16px;
  }

  & span {
    display: block;
    padding-right: 20px;
    font-size: 24px;
    font-weight: 600;
  }

  &::before {
    content: "";
    position: absolute;
    width: 14px;
    height: 14px;
    right: 24px;
    top: 24px;
    background-image: url("../assets/img/icons/stack.svg#list-inverse");
  }
}

.vacancies {
  &__title {
    margin-bottom: 35px;
    text-align: center;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
}

.vacancy {
  padding: 30px;
  display: flex;
  flex-direction: column;
  width: calc(50% - 10px);
  border-radius: 24px;
  background-color: var(--white);

  &__top {
    margin-bottom: 30px;
    padding-bottom: 25px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    border-bottom: 1px dashed #73947a33;

    & div {
      display: flex;
      flex-direction: column;
      gap: 10px;
    }
  }

  &__note {
    padding: 10px 20px;
    border-radius: 40px;
    font-size: 16px;
    border: 1px solid var(--border);

    &--accent {
      border: none;
      color: var(--white);
      background-color: var(--accent);
    }
  }

  &__list {
    margin-bottom: 25px;
    display: flex;
    gap: 24px;
  }

  &__item {
    display: flex;
    gap: 12px;

    &::before {
      content: "";
      width: 18px;
      height: 18px;
    }

    &--address::before {
      mask-image: url("../assets/img/icons/stack.svg#city");
      background-color: var(--accent);
    }

    &--time::before {
      background-image: url("../assets/img/icons/stack.svg#clock-round");
    }

    &--experience::before {
      background-image: url("../assets/img/icons/stack.svg#case");
    }
  }

  .requirements {
    margin: 32px 0;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }

  .requirement {
    padding: 10px 18px;
    display: flex;
    border-radius: 40px;
    font-size: 16px;
    background-color: var(--back-main);

    &.hidden {
      display: none;
    }
  }

  .button__more {
    padding: 12px 18px;
    height: auto;
  }

  &__bottom {
    display: flex;
    gap: 10px;
  }

  &__salary {
    padding: 12px;
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--main);
    border-radius: 40px;
    border: 1px solid var(--border);

    & span {
      margin-left: 5px;
      display: inline-block;
      font-weight: 600;
    }
  }
}

@media (max-width: 1200px) {
  .vacancy__bottom {
    flex-direction: column-reverse;
  }

  .vacancy__button {
    width: 100%;
  }
}

@media (max-width: 1023px) {
  .vacancies__numbers {
    flex-direction: column;
  }

  .vacancies__number {
    padding: 16px 20px;
    width: 100%;
    min-height: 83px;
    border-radius: 14px;

    & p {
      font-size: 14px;
    }

    & span {
      font-size: 18px;
    }

    &::before {
      width: 12px;
      height: 12px;
      right: 16px;
      top: 16px;
      background-size: 12px 12px;
    }
  }

  .vacancies {
    &__title {
      margin-bottom: 25px;
      text-align: left;
    }

    &__list {
      flex-direction: column;
    }
  }

  .vacancy {
    padding: 20px;
    width: 100%;
    border-radius: 16px;

    &__top {
      margin-bottom: 24px;
      padding-bottom: 20px;

      & div {
        gap: 16px;
      }

      & p:last-child {
        font-size: 14px;
      }
    }

    &__note {
      padding: 5px 16px;
      font-size: 14px;
    }

    &__list {
      margin-bottom: 20px;
      flex-direction: column;
      gap: 16px;
    }

    &__item {
      display: flex;
      gap: 12px;

      &::before {
        content: "";
        width: 15px;
        height: 15px;
        mask-size: 15px 15px;
      }
    }

    .requirements {
      margin: 24px 0;
      flex-direction: column;
    }

    .requirement {
      padding: 0;
      border-radius: 0;
      gap: 16px;
      background-color: transparent;
    }

    .requirement::before {
      content: "";
      margin-top: 3px;
      min-width: 16px;
      width: 16px;
      height: 16px;
      background-image: url("../assets/img/icons/stack.svg#list");
      background-size: 16px 16px;
    }

    .button__more {
      padding: 0;
      border: none;
      justify-content: flex-start;
      font-size: 16px;

      &:hover {
        color: var(--main);
        background-color: transparent;
      }
    }

    &__salary {
      flex-grow: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      color: var(--main);
      border-radius: 40px;
      border: 1px solid var(--border);

      & span {
        margin-left: 5px;
        display: inline-block;
        font-weight: 600;
      }
    }
  }
}