// doctors

.doctors {
  &__wrapper {
    position: relative;
  }

  &__title {
    margin-bottom: 30px;
  }

  &__buttons {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    width: 480px;
    align-items: center;
    gap: 30px;
  }

  &__link {
    width: 320px;
  }
}

.doctors__slider {
  overflow: hidden;
}

.doctor {
  padding: 16px 16px 16px 40px;
  display: flex;
  gap: 40px;
  height: auto;
  border-radius: 32px;
  background-color: var(--back-main);
}

.doctor__img {
  min-width: 315px;
  width: 315px;
  height: 320px;
  background: radial-gradient(88.32% 88.43% at 55.62% 60.63%, #C1CDC3 0%, #73947A 100%);
  border-radius: 26px;
  overflow: hidden;
}

.doctor picture {
  width: 100%;
  height: 100%;
}

.doctor img {
  margin: 0 auto;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.doctor:hover {
  outline: 1px solid var(--main);
  outline-offset: -1px;
  transition: outline 0.7s;
}

.doctor__text {
  padding: 24px 0;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.doctor__name {
  margin-bottom: 15px;
  font-size: 20px;
  font-weight: 600;
}

.doctor__prof {
  margin-bottom: 24px;
  font-size: 18px;
  font-weight: 600;
  color: var(--main);
}

.doctor__work {
  margin-bottom: auto;
  margin-right: auto;
  padding: 16px 18px;
  border-radius: 40px;
  color: #ffffffce;
  background-color: var(--accent);
}

.doctor__description {
  font-size: 16px;
}

.doctors-page {
  margin-bottom: 110px;

  .doctors__list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }

  & .doctor {
    width: calc(50% - 10px);
  }

}

@media (max-width: 1400px) {
  .doctor {
    flex-direction: column-reverse;
    gap: 20px;
  }

  .doctor__img {
    min-width: 100%;
    width: 100%;
    min-height: 280px;
    border-radius: 14px;
  }
}

@media (max-width: 768px) {
  .doctors {
    &__title {
      margin-bottom: 20px;
    }

    &__buttons {
      position: static;
      width: 100%;
    }

    &__link {
      width: 100%;
    }
  }

  .doctors__slider {
    margin-bottom: 30px;
  }

  .doctor {
    padding: 10px 10px 24px 10px;
    border-radius: 16px;
  }

  .doctor__text {
    position: relative;
    padding: 0 9px;
    margin-bottom: -42px;
  }

  .doctor__name {
    margin-bottom: 10px;
    font-size: 18px;
  }

  .doctor__prof {
    margin-bottom: 14px;
    font-size: 14px;
  }

  .doctor__work {
    position: absolute;
    left: 10px;
    top: -66px;
    padding: 12px 14px;
    font-size: 14px;
    line-height: 100%;
  }

  .doctor__description {
    font-size: 14px;
  }

  .doctors-page .doctors__list {
    flex-direction: column;
    gap: 10px;
  }

  .doctors-page .doctor {
    width: 100%;
  }

  .doctors-page .doctor__text {
    margin-bottom: 0;
  }
}

.doctor-page {
  &__wrapper {
    display: flex;
    gap: 100px;
  }

  &__sidebar {
    min-width: 27%;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__timeline {
    padding: 40px;
    border-radius: 32px;
    background-color: var(--back-main);
  }

  &__timeline ul {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    gap: 24px;

    & li {
      display: flex;
      justify-content: space-between;
      gap: 20px;

      & span {
        color: var(--main);
        text-align: right;
      }
    }

    & li:not(:last-child) {
      padding-bottom: 24px;
      border-bottom: 1px dashed var(--border);
    }
  }

  &__banner {
    padding: 40px;
    display: flex;
    flex-direction: column;
    gap: 24px;
    border-radius: 32px;
    color: var(--white);
    background-color: var(--accent);

    &-wrapper {
      position: relative;
      margin-bottom: 26px;
      padding: 25px;
      display: flex;
      flex-direction: column;
      gap: 40px;
      border-radius: 16px;
      background-color: #ffffff29;

      & span {
        font-size: 36px;
        font-weight: 600;
      }

      &::before {
        content: "";
        position: absolute;
        width: 22px;
        height: 22px;
        right: 21px;
        top: 25px;
        background-image: url("../assets/img/icons/stack.svg#check");
      }
    }
  }

  &__button {
    margin-top: auto;
    color: var(--black);
    background-color: var(--white);

    & span {
      background-color: var(--accent);

      &::before {
        background-color: var(--white);
      }
    }

    &:hover {
      background-color: var(--back-accent);

      & span::before {
        background-color: var(--back-accent);
      }
    }
  }

  &__main {
    display: flex;
    flex-direction: column;
    gap: 60px;
  }

  &__title {
    margin-bottom: 35px;
  }

  &__flex.text-content ul {
    margin-bottom: 0;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }

  &__item {
    position: relative;
    padding: 32px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: calc(50% - 10px);
    min-height: 210px;
    border-radius: 24px;
    background-color: var(--back-main);

    &::before {
      content: "";
      position: absolute;
      width: 18px;
      height: 18px;
      right: 29px;
      top: 29px;
      mask-image: url("../assets/img/icons/stack.svg#date");
      background-color: var(--main);
    }
  }

  &__date {
    margin-bottom: auto;
    color: var(--main);
  }

  &__name {
    margin: 20px 0;
    font-size: 20px;
    font-weight: 600;
  }

  &__description {
    font-size: 16px;
  }
}

.documents__top {
  margin-bottom: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.documents__slider {
  overflow: hidden;
  height: 100%;

  .document {
    padding: 16px;
    height: auto;
    border-radius: 32px;
    background-color: var(--white);

    & img {
      width: 100%;
      height: auto;
    }
  }

  .doctor-page__item {
    padding: 30px 16px 32px 16px;
    width: 100%;
    background-color: var(--white);
  }
}

@media (max-width: 1220px) {
  .doctor-page__timeline {
    padding: 40px 20px;
  }

  .doctor-page__banner {
    padding: 20px;
  }
}

@media (max-width: 1023px) {
  .doctor-page {
    &__wrapper {
      flex-direction: column;
      gap: 60px;
    }

    &__sidebar {
      min-width: 100%;
    }

    &__timeline {
      border-radius: 16px;
      background-color: var(--back-main);
    }

    &__timeline ul {
      margin-top: 30px;
      gap: 20px;

      & li:not(:last-child) {
        padding-bottom: 15px;
      }
    }

    &__banner {
      gap: 16px;
      border-radius: 16px;

      &-wrapper {
        margin-bottom: 20px;
        padding: 20px;
        gap: 30px;
        border-radius: 16px;

        & span {
          font-size: 24px;
        }

        &::before {
          width: 18px;
          height: 18px;
          right: 19px;
          top: 19px;
          background-size: 18px 18px;
        }
      }
    }

    &__main {
      gap: 40px;
    }

    &__title {
      margin-bottom: 20px;
    }

    &__list {
      flex-direction: column;
    }

    &__item {
      padding: 20px;
      width: 100%;
      min-height: 163px;
      border-radius: 16px;

      &::before {
        width: 14px;
        height: 14px;
        right: 20px;
        top: 24px;
        mask-size: 14px 14px;
      }
    }

    &__date {
      margin-bottom: 25px;
    }

    &__name {
      margin: 0 0 16px 0;
      font-size: 18px;
    }

    &__description {
      font-size: 14px;
    }
  }

  .documents__top {
    margin-bottom: 25px;
  }

  .documents__slider {
    .document {
      padding: 10px;
      border-radius: 16px;
    }

    .doctor-page__item {
      padding: 25px 3px 20px 3px;
    }
  }
}