// gallery

.gallery {
  &__top {
    margin-bottom: 50px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__title {
    margin-bottom: 30px;
  }

  &__item {
    position: relative;
    width: calc(33% - 10px);
    border-radius: 26px;
    overflow: hidden;
  }

  &__img {
    height: 320px;
    width: 100%;
    object-fit: cover;
    object-position: center;
  }

  &__note {
    position: absolute;
    top: 15px;
    right: 15px;
    padding: 13px;
    border-radius: 40px;
    background-color: var(--accent-light);
  }

  &__link {
    margin: 0 auto;
    width: 320px;
  }
}

.gallery__list,
.documents__list {
  margin-bottom: 30px;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.gallery .tabs-mobile {
  width: 39%;
}

.gallery__item a,
.documents__item a {
  position: relative;
  display: block;
  width: 100%;

  &::before {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 48px;
    height: 48px;
    opacity: 0;
    transition: opacity 0.7s;
    background: url("../assets/img/icons/stack.svg#scale") center no-repeat;
    z-index: 3;
  }

  &:hover {
    &::before {
      opacity: 1;
      transition: opacity 0.7s;
    }
  }
}

.documents__item {
  width: calc(20% - 10px);
  border-radius: 26px;
  overflow: hidden;

  & img {
    margin: 0 auto;
    border-radius: 26px;
    max-width: 100%;
    max-height: 400px;
    object-fit: cover;
  }
}

.documents-page {
  margin-bottom: 140px;

  .documents__item {
    padding: 16px;
    border-radius: 24px;
    background-color: var(--back-main);

    & p {
      margin-top: 24px;
      font-size: 20px;
      text-align: center;
    }
  }
}

@media (max-width: 1300px) {
  .documents__item {
    width: calc(25% - 10px);
  }
}

@media (max-width: 900px) {
  .documents__item {
    width: calc(33% - 10px);
  }
}

@media (max-width: 768px) {
  .gallery {
    &__top {
      margin-bottom: 30px;
      flex-direction: column;
      gap: 0;
    }

    &__title {
      margin-bottom: 20px;
    }

    &__list {
      max-height: 830px;
      overflow: hidden;
    }

    &__item {
      width: 100%;
      border-radius: 16px;
    }

    &__img {
      height: 220px;
    }

    &__note {
      top: auto;
      right: auto;
      bottom: 10px;
      left: 10px;
      padding: 10px 14px;
    }

    &__link {
      width: 100%;
    }
  }

  .gallery__list,
  .documents__list {
    flex-direction: column;
  }

  .gallery .tabs-mobile {
    width: 100%;
  }

  .documents__list {
    max-height: 800px;
    overflow: hidden;
  }

  .documents__item {
    width: 100%;

    & img {
      margin: 0 auto;
      max-height: 400px;
    }
  }
}

.main-tabs {
  padding: 3px;
  display: flex;
  flex-wrap: wrap;
  border-radius: 16px;
  background-color: #73947a33;
}

.main-tab {
  padding: 20px;
  min-width: 297px;
  flex-grow: 1;
  text-align: center;
  border-radius: 14px;
  color: #3131319d;

  &:hover {
    background-color: #74947a33;
  }
}

.main-tab.active {
  color: var(--white);
  background-color: var(--main);
}

.tab-content {
  transform: scaleY(0);
  transform-origin: top center;
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  transition: transform 1s,
    opacity 1s,
    max-height 1s;
}

.tab-content.active {
  transform: scaleY(1);
  opacity: 1;
  max-height: 5000px;
  transition: transform 1s,
    opacity 1s,
    max-height 1s;
}

.tab-content__top {
  margin-top: 70px;
  display: flex;
  justify-content: space-between;
}

// gallery-page

.gallery-page {
  margin-bottom: 110px;

  .gallery-page__list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }

  .gallery__slide {
    position: relative;
    display: none;
    width: calc(33% - 10px);
    height: 360px;
    border-radius: 32px;
    overflow: hidden;

    & picture {
      width: 100%;
      height: 100%;
    }
  }

  .gallery__img {
    height: 100%;
    object-fit: cover;
  }

  .gallery__slide.active {
    display: flex;
  }

  .gallery__description {
    position: absolute;
    left: 12px;
    bottom: 12px;
    padding: 24px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    border-radius: 20px;
    background-color: var(--back-main);

    & span {
      font-size: 20px;
      font-weight: 600;
    }
  }

  .tabs-mobile {
    margin-bottom: 50px;
  }
}

@media (max-width: 1023px) {
  .tabs-mobile {
    position: relative;
    width: 100%;
  }

  .tabs-mobile span {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px;
    border-radius: 12px;
    font-size: 16px;
    background-color: var(--back-main);
    cursor: pointer;

    &::after {
      content: "";
      display: block;
      width: 14px;
      height: 7px;
      background: url("../assets/img/icons/stack.svg#down");
      transition: transform 0.7s;
    }
  }

  .gallery .tabs-mobile span {
    background-color: var(--white);
  }

  .main-tabs {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: calc(100% + 10px);
    left: 0;
    right: 0;
    padding: 24px;
    width: 100%;
    flex-direction: column;
    gap: 16px;
    border-radius: 8px;
    background-color: var(--back-main);
    transition: opacity 0.7s;
  }

  .main-tab {
    padding: 10px;
    width: 100%;
    min-width: 100%;
  }

  .tab-content__top {
    margin-top: 50px;
    flex-direction: column;
    gap: 20px;
  }

  .tabs-mobile.opened {
    & span {
      outline: 1px solid var(--border);
    }

    &::after {
      transform: rotate(180deg);
      transition: transform 0.7s;
    }

    & .main-tabs {
      opacity: 1;
      visibility: visible;
      z-index: 2;
      transition: opacity 0.7s;
      background-color: var(--white);
    }
  }

  .faq-page .tabs-mobile.opened .main-tabs {
    background-color: var(--back-main);
  }
}

@media (max-width: 768px) {
  .gallery-page {
    .gallery__list {
      flex-direction: column;
    }

    .gallery__slide {
      width: 100%;
      height: auto;
      flex-direction: column;
      gap: 6px;
      border-radius: 16px;
    }

    .gallery__description {
      position: static;
      padding: 20px;
      border-radius: 16px;

      & span {
        font-size: 16px;
      }
    }

    .tabs-mobile {
      margin-bottom: 40px;
    }
  }

  .documents-page {
    margin-bottom: 70px;

    .documents__list {
      max-height: 100%;
    }

    .documents__item {
      padding: 12px;
      border-radius: 16px;

      & p {
        margin-top: 24px;
        font-size: 16px;
      }
    }
  }
}