// servise-features

.servise-features {
  &__title {
    margin-bottom: 35px;
    text-align: center;
  }

  & .lead {
    text-align: center;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
}

.servise-feature {
  padding: 22px 22px 50px 40px;
  border-radius: 32px;
  width: calc(33% - 5px);
  min-height: 240px;
  background-color: var(--back-main);
  display: flex;
  flex-direction: column;

  &__wrapper {
    margin-bottom: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
  }

  &__name {
    color: var(--main);
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 54px;
    min-width: 54px;
    height: 54px;
    border-radius: 12px;
    background-color: var(--main);
  }

  &__description {
    margin-top: auto;
  }
}

@media (max-width: 1023px) {
  .servise-features {
    &__title {
      margin-bottom: 20px;
      text-align: left;
    }

    & .lead {
      text-align: left;
    }
  }

  .servise-feature {
    padding: 12px 12px 20px 20px;
    border-radius: 16px;
    width: calc(50% - 10px);
    min-height: 181px;

    &__wrapper {
      margin-bottom: 30px;
    }

    &__icon {
      width: 46px;
      min-width: 46px;
      height: 46px;
      border-radius: 10px;

      & svg {
        width: 18px;
        height: 20px;
      }
    }

    &__description {
      font-size: 14px;
    }
  }
}

@media (max-width: 768px) {
  .servise-features__list {
    flex-direction: column;
    gap: 8px;
  }

  .servise-feature {
    width: 100%;
  }
}