/* header */
.header {
  position: relative;
  margin-bottom: 94px;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px 0;
  }

  &__menu {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  &__left {
    padding: 0 30px 0 16px;
    display: flex;
    gap: 30px;
    border-radius: 40px;
    background-color: var(--white);
  }
}

.search--mobile {
  display: none;
}

.search--desktop {
  display: none;
}

.search,
.intro__search {
  position: relative;
}

.search .form__label {
  width: 100%;
}

.search::after,
.intro__search::after {
  content: "";
  position: absolute;
  display: block;
  right: 20px;
  top: 20px;
  width: 16px;
  height: 16px;
  background-image: url("../assets/img/icons/stack.svg#search");
}

.search__wrapper {
  transform: scaleY(0);
  transform-origin: top;
  opacity: 0;
  transition: transform 0.5s ease, opacity 0.5s ease;
  height: 0;
  overflow: hidden;
}

.search__wrapper--error {
  display: none;
}

.search__wrapper.active {
  transform: scaleY(1);
  opacity: 1;
  height: auto;
  transition: transform 0.5s ease, opacity 0.5s ease;
}

.search__list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.footer .search__list {
  color: var(--black);
}

.search__wrapper--error.active {
  margin-top: 8px;
  padding: 24px;
  display: flex;
  flex-direction: column;
  gap: 24px;
  background-color: var(--white);
  border-radius: 24px;
}

.footer .search__wrapper.active,
.footer .search__wrapper--error.active,
.intro__search .search__wrapper.active,
.intro__search .search__wrapper--error.active {
  margin-top: 0;
  padding-top: 20px;
  position: absolute;
  top: 100%;
  width: 100%;
  left: 0;
  z-index: 2;
}

.intro__search .search__list {
  background-color: var(--white);
  border: solid 1px var(--border);
}

.intro__search .search__wrapper--error{
  border: solid 1px var(--border);
}

.search__list li:hover,
.search__list a:hover {
  color: var(--main);
}

.intro__search .search__list li {
  cursor: pointer;
}

.search__wrapper--error p {
  font-size: 16px;
}

.search__title {
  padding-top: 32px;
  font-size: 20px;
  font-weight: 600;
  border-top: 2px dashed #73947a33;
}

.search__list-error {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.search__list-error li {
  padding: 10px 10px 10px 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: calc(25% - 10px);
  border-radius: 16px;
  background-color: var(--back-main);
}

.search__list-error li:hover {
  background-color: var(--main);
  transition: background-color 0.7s;
}

.search__list-error li span {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 56px;
  width: 56px;
  height: 56px;
  border-radius: 12px;
  background-color: var(--white);
}

.search__list-error li span::before {
  content: "";
  width: 17px;
  height: 13px;
  background-image: url("../assets/img/icons/stack.svg#arrow-right");
}

.search--desktop {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s ease, visibility 0.5s ease;
}

.search--desktop.active {
  opacity: 1;
  visibility: visible;
  z-index: 10;
}

.search--desktop .search__input {
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.5s ease 0.1s;
  width: 100%;
  background-color: var(--white);
}

.search--desktop.active .search__input {
  transform: scaleX(1);
}

.search__list {
  padding: 32px;
  background-color: var(--white);
  border-radius: 24px;
}

.menu-mobile {
  display: none;
}

.logo {
  margin-right: 30px;
  display: flex;
  gap: 8px;
  align-items: center;
  max-width: 165px;
}

.logo__text {
  font-size: 16px;
  font-weight: 600;
  color: var(--main);
}

.main-navigation {
  display: flex;
  align-items: center;
  font-weight: 600;
}

.main-navigation__list {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  gap: 30px;
}

.main-navigation__item {
  position: relative;
}

.main-navigation__title {
  margin-right: 30px;
  padding: 15px 24px;
  display: flex;
  border-radius: 40px;
  background-color: var(--accent-light);
  transition: color 0.7s;
  cursor: pointer;
}

.main-navigation__link {
  position: relative;
  padding: 15px 0;
  display: flex;
}

.main-navigation__link:hover {
  color: var(--main);
  transition: color 0.7s;
}

.submenu {
  padding-top: 8px;
  position: absolute;
  left: 0;
  top: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.7s, visibility 0.7s;
}

.submenu__list {
  padding: 24px;
  display: flex;
  flex-direction: column;
  gap: 24px;
  min-width: 280px;
  border-radius: 16px;
  overflow: hidden;
  background-color: var(--white);
}

.submenu__item {
  cursor: pointer;
}

.submenu__link {
  transition: color 0.7s, background-color 0.7s;
}

.submenu__link:hover {
  color: var(--main);
}

.submenu--secondary {
  padding-left: 6px;
  position: absolute;
  left: 100%;
  top: 36px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.7s, visibility 0.7s;
}

@media (min-width: 1024px) {

  .main-navigation__item:hover .submenu,
  .submenu__item:hover .submenu--secondary {
    opacity: 1;
    visibility: visible;
    z-index: 3;
    transition: opacity 0.7s, visibility 0.7s;
  }
}

.header__phone {
  font-size: 20px;
  font-weight: 600;
  transition: color 0.7s;
}

.header__phone:hover {
  color: var(--main);
  transition: color 0.7s;
}

.header__buttons {
  display: flex;
  gap: 10px;
}

.button-small {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 54px;
  height: 54px;
  border-radius: 50%;
  background-color: var(--white);
  transition: opacity 0.7s;

  & svg {
    fill: var(--main);
  }
}

.button-small:hover {
  opacity: 0.7;
  transition: opacity 0.7s;
}

.intro:not(.block-back-accent) .button-small {
  background-color: var(--back-main);
}

.header__button {
  width: 271px;
}

.socials {
  display: flex;
  gap: 10px;
}

.socials__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background-color: var(--main);
}

.socials__link:hover {
  background-color: var(--accent);
}

@media (min-width: 1024px) AND (max-width: 1300px) {
  .intro__picture {
    max-width: 450px;
    max-height: 450px;
  }

  .intro__picture img {
    width: 100%;
    height: 100%;
  }
}

@media (max-width: 1023px) {
  .header {
    margin-bottom: 32px;
    padding: 12px 20px;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10;
    background-color: var(--white);

    &__left {
      padding: 0;
      border-radius: 0;
      background-color: transparent;
    }
  }

  .header .logo__text,
  .main-navigation,
  .header__phone,
  .button-small {
    display: none;
  }

  .search--mobile {
    display: block;
  }

  .search__input {
    width: 100%;
  }

  .header__buttons {
    gap: 15px;
  }

  .button.header__button {
    font-size: 14px;
    height: 38px;
    width: max-content;
    min-width: max-content;
    background-color: var(--accent);
  }

  .header__burger {
    position: relative;
    display: flex;
    width: 38px;
    height: 38px;
    border-radius: 50%;
    background-color: var(--back-main);
  }

  .header__burger::before {
    content: "";
    position: absolute;
    width: 14px;
    height: 1px;
    left: 12px;
    top: 16px;
    background-color: var(--main);
    border-radius: 5px;
  }

  .header__burger::after {
    content: "";
    position: absolute;
    width: 14px;
    height: 1px;
    left: 12px;
    bottom: 16px;
    border-radius: 5px;
    background-color: var(--main);
    transition: transform 0.7s;
  }

  .header__burger--opened.header__burger::before {
    top: 50%;
    transform: rotate(45deg);
    box-shadow: none;
    transition: transform 0.7s;
  }

  .header__burger--opened.header__burger::after {
    top: 50%;
    bottom: auto;
    transform: rotate(-45deg);
    transition: transform 0.7s;
  }

  .logo {
    margin-right: 0;
    max-width: 38px;

    & svg {
      width: 38px;
      height: 38px;
    }
  }

  .menu-mobile.opened {
    margin-bottom: 50px;
    position: absolute;
    padding: 20px;
    padding-bottom: 150px;
    top: 100%;
    left: 0;
    display: flex;
    flex-direction: column;
    gap: 30px;
    width: 100%;
    background-color: var(--white);
    z-index: 20;
    height: 100vh;
    overflow-y: auto;
  }

  .menu-mobile.opened .main-navigation {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .main-navigation__list {
    display: flex;
    flex-direction: column;
    gap: 8px;
    width: 100%;
    align-items: flex-start;
  }

  .main-navigation__title {
    margin-right: 0;
    margin-bottom: 8px;
    padding: 20px;
    width: 100%;
    border-radius: 12px;
  }

  .main-navigation__item {
    padding: 5px 20px;
    width: 100%;
    background-color: var(--back-main);
    border-radius: 12px;
  }

  .main-navigation__title {
    margin-bottom: 0;
    display: block;
    border-radius: 12px;
    background-color: var(--accent-light);
  }

  .main-navigation__link,
  .main-navigation__title {
    justify-content: space-between;
    width: 100%;
  }

  .main-navigation__item:has(.submenu) .main-navigation__link::after {
    content: "";
    margin-top: 5px;
    width: 14px;
    height: 7px;
    background-image: url("../assets/img/icons/stack.svg#down");
    transition: transform 0.7s;
  }

  .main-navigation__item.opened .main-navigation__link::after {
    transform: rotate(180deg);
    transition: transform 0.7s;
  }

  .submenu {
    padding-bottom: 15px;
    position: static;
    display: none;
    font-size: 14px;
    max-height: 0;
    transition: max-height 0.7s;
  }

  .submenu__list {
    padding: 0;
    min-width: 0;
    width: 100%;
    gap: 16px;
    border-radius: 0;
    background-color: transparent;
  }

  .main-navigation__item.opened .submenu {
    display: flex;
    opacity: 0.8;
    max-height: 100vh;
    visibility: visible;
    transition: max-height 0.7s;
  }

  .submenu__link {
    position: relative;
    padding: 0;
    display: flex;
    justify-content: space-between;
  }

  .submenu__item:has(.submenu--secondary) .submenu__link::after {
    content: "";
    width: 14px;
    height: 7px;
    background-image: url("../assets/img/icons/stack.svg#down");
    transition: transform 0.7s;
  }

  .submenu__link.opened::after {
    transform: rotate(180deg);
    transition: transform 0.7s;
  }

  .submenu--secondary {
    position: static;
    left: auto;
    top: auto;
    display: none;
  }

  .submenu__link.opened~.submenu--secondary {
    padding: 0;
    padding-top: 10px;
    display: flex;
    opacity: 0.7;
    visibility: visible;
    transition: opacity .7s, visibility .7s;
  }

  .submenu--secondary .submenu__list {
    gap: 10px;
  }

  .submenu--secondary .submenu__link::after {
    display: none;
  }

  .header__contacts {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  .header__contacts span {
    font-size: 18px;
    font-weight: 600;
  }

  .socials__link {
    width: 52px;
    height: 52px;
  }

  .search__input {
    transform: scaleX(1);
  }

  .search__list {
    padding: 18px;
    border-radius: 16px;
  }

  .search__list-error {
    padding: 18px;
    border-radius: 16px;
  }

  .search__list-error li {
    padding: 6px 6px 6px 20px;
    width: 100%;
    border-radius: 12px;
  }

  .search__list-error li span {
    min-width: 52px;
    width: 52px;
    height: 52px;
    border-radius: 10px;
  }

  .footer .search__wrapper.active,
  .footer .search__wrapper--error.active,
  .intro__search .search__wrapper.active,
  .intro__search .search__wrapper--error.active {
    position: static;
    padding: 18px;
    border-radius: 16px;
  }
}