.contacts {
  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin-bottom: 40px;
    text-align: center;
  }

  & .lead {
    text-align: center;
  }

  &__list {
    display: flex;
    gap: 10px;
  }

  &__item {
    padding: 22px 22px 40px 40px;
    display: flex;
    flex-direction: column;
    gap: 60px;
    width: calc(33% - 4px);
    border-radius: 24px;
    background-color: var(--back-accent);

    & a:hover {
      color: var(--main);
    }

    &-top {
      margin-bottom: auto;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &-bottom {
      span {
        display: block;
        margin-top: 30px;
        padding: 10px 18px;
        width: max-content;
        font-size: 16px;
        border-radius: 40px;
        border: 1px solid #cba28880;
      }

      & p {
        padding-right: 18px;
      }
    }

  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 54px;
    height: 54px;
    border-radius: 12px;
    background-color: var(--accent);

    & svg {
      fill: var(--white);
    }
  }

  &__map {
    margin-top: 20px;
    min-height: 500px;
    width: 100%;
    border-radius: 32px;
    overflow: hidden;
  }
}

.contacts__link {
  display: block;
  margin-bottom: 20px;
  font-size: 32px;
  font-weight: 600;
  line-height: 130%;
}

.block-back-main {
  & .contacts__item {
    background-color: var(--white);
  }

  & .contacts__icon {
    background-color: var(--main);
  }
}

#map {
  width: 100%;
  height: 500px;
}

.contacts__socials-list {
  display: flex;
  gap: 10px;
}

.contacts__socials-item {
  padding: 21px;
  width: calc(33% - 4px);
  border-radius: 40px;
  color: var(--white);

  & a {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
  }

  &--tg {
    background-color: #27A6E5;
  }

  &--wa {
    background-color: #48C95F;
  }

  &--vk {
    background-color: #0077FF;
  }

  &:hover {
    opacity: 0.8;
  }
}

@media (max-width: 1023px) {
  .contacts {
    &__title {
      margin-bottom: 20px;
      text-align: left;
    }

    & .lead {
      text-align: left;
    }

    &__list {
      flex-direction: column;
      gap: 8px;
    }

    &__item {
      padding: 12px 12px 20px 20px;
      gap: 30px;
      width: 100%;
      border-radius: 16px;

      &-top {
        margin-bottom: 0;
      }

      &-bottom {
        span {
          margin-top: 20px;
          padding: 10px 16px;
          width: 100%;
          font-size: 14px;
        }

        & p {
          padding-right: 0;
        }
      }

    }

    &__icon {
      width: 46px;
      height: 46px;
      border-radius: 10px;

      & svg {
        width: 18px;
        height: 18px;
      }
    }

    &__map {
      margin-top: 30px;
      border-radius: 16px;
    }
  }

  .contacts__link {
    margin-bottom: 15px;
    font-size: 18px;
  }

  #map {
    width: 100%;
    height: 500px;
  }

  .contacts__socials-list {
    flex-direction: column;
  }

  .contacts__socials-item {
    padding: 18px;
    width: 100%;

    & a {
      gap: 16px;
    }
  }
}