// text-content
blockquote {
  margin-top: 35px;
  padding: 32px 25px;
  border-radius: 24px;
  color: var(--white);
  background-color: var(--main);
}

.text-content {
  .container {
    max-width: 1200px;
  }
  
  &__wrapper {
    max-height: 750px;
    overflow: hidden;
    transition: max-height 0.7s;
  }

  & a {
    font-weight: 600;
    color: var(--main);
  }

  & a:hover {
    opacity: 0.8;
    transition: opacity 0.7s;
  }

  & h2,
  h3,
  h4,
  h5 {
    font-weight: 600;
    line-height: 120%;
  }

  & h2 {
    margin-top: 50px;
    margin-bottom: 40px;
    font-size: 50px;
  }

  & h3 {
    margin-top: 40px;
    margin-bottom: 30px;
    font-size: 36px;
  }

  & h4 {
    margin-top: 30px;
    margin-bottom: 30px;
    font-size: 28px;
  }

  & p {
    margin-bottom: 30px;
  }

  & ul {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  ol {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  & ul,
  ol {
    margin-bottom: 30px;
  }

  & ol li {
    position: relative;
    padding-left: 42px;
    padding-top: 5px;
    min-height: 30px;
  }

  & ol li::marker {
    content: none;
  }

  & ol li::before {
    content: counter(list-item);
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    color: var(--white);
    background-color: var(--main);
    border-radius: 50%;
    font-size: 14px;
    line-height: 140%;
    background-image: none;
  }

  .table {
    margin-bottom: 24px;
    display: block;
    overflow-x: auto;
  }

  table {
    border-collapse: collapse;
    border-radius: 24px;
    position: relative;
    overflow: hidden;
    outline: 1px solid var(--main);
    outline-offset: -1px;
  }

  table th,
  table td {
    padding: 16px;
    border: 1px solid var(--main);
  }

  table th {
    background-color: var(--back-main);
  }

  table tr:nth-child(2n) {
    background-color: var(--white);
  }

  & img {
    border-radius: 24px;
  }

  *:first-child {
    margin-top: 0;
  }

  .details {
    margin-bottom: 60px;
  }

  .details[open] {
    background-color: var(--white);
  }

  .text-content__nav ul {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
}

.button.button--open {
  margin-top: 40px;
  width: 260px;
  font-size: 18px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &::after {
    content: "";
    display: block;
    width: 14px;
    height: 7px;
    background: url("../assets/img/icons/stack.svg#down");
    transition: transform 0.7s;
  }
}

.button.button--open:hover {
  color: var(--main);
  background-color: var(--back-main);
  border: 1px solid var(--main);
  transition: background-color 0.7s, border 0.7s;
}

.button--open.opened {
  &::after {
    transform: rotate(180deg);
    transition: transform 0.7s;
  }
}


@media (max-width: 768px) {
  blockquote {
    padding: 24px 20px;
    border-radius: 16px;

    & p {
      margin-bottom: 0;
    }
  }

  .text-content {
    &__wrapper {
      max-height: 1610px;
    }

    blockquote p {
      margin-bottom: 0;
    }

    & h2,
    h3,
    h4,
    h5 {
      line-height: 110%;
    }

    & h2 {
      margin-top: 40px;
      margin-bottom: 34px;
      font-size: 26px;
    }

    & h3 {
      margin-top: 30px;
      margin-bottom: 24px;
      font-size: 20px;
    }

    & h4 {
      margin-top: 20px;
      margin-bottom: 24px;
      font-size: 18px;
    }

    & p {
      margin-bottom: 24px;
    }

    & ul,
    ol {
      margin-bottom: 24px;
    }

    & ol li::marker {
      content: none;
    }

    & ol li::before {
      content: counter(list-item);
      position: absolute;
      left: 0;
      top: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 30px;
      height: 30px;
      color: var(--white);
      background-color: var(--main);
      border-radius: 50%;
      font-size: 14px;
      line-height: 140%;
    }

    table {
      border-radius: 16px;
    }

    & img {
      border-radius: 16px;
    }
  }

  .button.button--open {
    margin-top: 20px;
    font-size: 16px;
  }
}

.check {
  margin-top: 50px;
  display: flex;
  gap: 10px;
}

.check-doctor {
  padding: 32px;
  width: calc(50% - 5px);
  border-radius: 24px;
  background-color: #ddcabe33;

  &__top {
    opacity: 0.6;
  }

  &__wrapper {
    margin-bottom: 32px;
    padding-bottom: 32px;
    display: flex;
    gap: 20px;
    border-bottom: 1px solid var(--border);
  }

  &__img {
    width: 120px;
    min-width: 120px;
    height: 120px;
    border-radius: 16px;
    background: radial-gradient(79.76% 79.78% at 52.02% 69.27%, #C1CDC3 0%, #73947A 100%);
    overflow: hidden;

    & img {
      width: 100%;
      height: 100%;
      border-radius: 0;
      object-fit: cover;
    }
  }

  &__text {
    padding-right: 100px;
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__name {
    margin-bottom: 20px;
    font-size: 20px;
    font-weight: 600;
  }

  &__work {
    margin-top: auto;
    color: var(--main);
  }

  &__date {
    display: flex;
    justify-content: space-between;

    & p {
      margin-bottom: 0;
      opacity: 0.6;
    }
  }
}

@media (max-width: 1023px) {
  .check {
    margin-top: 40px;
    flex-direction: column;
  }

  .check-doctor {
    padding: 20px;
    width: 100%;
    border-radius: 16px;

    &__top,
    &__prof {
      font-size: 14px;
    }

    &__wrapper {
      margin-bottom: 20px;
      padding-bottom: 20px;
      gap: 12px;
    }

    &__img {
      width: 60px;
      min-width: 60px;
      height: 60px;
      border-radius: 10px;
    }

    &__text {
      padding-right: 0;
    }

    &__name {
      margin-bottom: 12px;
      font-size: 16px;
    }

    &__work {
      margin-top: 16px;
    }

    &__date {
      font-size: 14px;
    }
  }
}