.tariffs {
  &__title {
    margin-bottom: 40px;
    text-align: center;
  }

  & .tariffs__title+.lead {
    padding: 0 300px;
    text-align: center;
  }

  &__slider {
    margin-bottom: 50px;
    overflow: hidden;
    height: 100%;
  }

  & .prices__flex {
    width: 56%;
  }
}

@media (min-width: 1024px) {
  .tariffs .swiper-wrapper {
    display: flex;
    gap: 10px;
  }
}

.tariff {
  padding: 26px 26px 40px 40px;
  width: calc(33% - 10px);
  height: auto;
  display: flex;
  flex-direction: column;
  border-radius: 32px;
  background-color: var(--back-main);

  &__top {
    margin-bottom: 32px;
    padding-bottom: 32px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    gap: 40px 0;
    border-bottom: 1px dashed var(--border);
  }

  &__name {
    margin-top: 14px;
  }

  &__bage {
    padding: 10px 18px;
    border-radius: 40px;
    font-size: 16px;
    border: 1px solid var(--main);

    &--accent {
      border: none;
      color: var(--white);
      background-color: var(--accent);
    }
  }

  &__description {
    opacity: 0.8;
  }

  &__cost {
    margin-bottom: 35px;
    display: flex;
    justify-content: space-between;

    & span {
      font-weight: 600;
      font-size: 20px;
      line-height: 150%;
      color: var(--main);
    }
  }

  & ul {
    margin-bottom: 24px;
    display: flex;
    flex-direction: column;
    gap: 20px;

    & li {
      display: flex;
    }

    & li.hidden {
      display: none;
    }
  }

  .button__more {
    margin-bottom: 40px;
    color: var(--main);
    text-align: left;
    cursor: pointer;

    &:hover {
      opacity: 0.8;
      transition: opacity 0.7s;
    }
  }

  &__button {
    margin-top: auto;
  }
}

@media (max-width: 1023px) {
  .tariffs {
    &__title {
      margin-bottom: 24px;
      text-align: left;
    }

    & .tariffs__title+.lead {
      padding: 0;
      text-align: left;
    }

    & .prices__flex {
      width: 100%;
    }
  }

  .tariff {
    padding: 24px 20px;
    border-radius: 16px;

    &__top {
      margin-bottom: 20px;
      padding-bottom: 20px;
      gap: 24px;
    }

    &__name {
      margin-top: 0;
      width: 46%;
    }

    &__bage {
      padding: 11px 14px;
      border-radius: 40px;
      font-size: 14px;
      line-height: 100%;
    }

    &__description {
      font-size: 14px;
    }

    &__cost {
      margin-bottom: 20px;
      font-size: 14px;

      & span {
        font-size: 16px;
      }
    }

    & ul {
      margin-bottom: 14px;
      gap: 10px;
    }

    .button__more {
      margin-bottom: 24px;
    }

    &__button {
      margin-top: auto;
    }
  }
}