// about

.about {
  &__wrapper {
    position: relative;
  }

  &__title {
    margin-bottom: 40px;
  }

  &__button {
    position: absolute;
    width: 320px;
    top: 0;
    right: 0;
  }

  &__bottom {
    display: flex;
    gap: 10px;
  }

  &__image {
    height: 100%;
    object-fit: cover;
    border-radius: 32px;
  }

  &__text {
    position: relative;
    padding: 40px;
    border-radius: 32px;
    background-color: var(--white);

    &::before {
      content: "";
      position: absolute;
      width: 16px;
      height: 16px;
      right: 40px;
      top: 40px;
      background-image: url("../assets/img/icons/stack.svg#dote");
    }
  }

  & .title-small {
    margin-bottom: 20px;
  }
}

@media (max-width: 1023px) {
  .about {
    &__title {
      margin-bottom: 20px;
    }

    &__button {
      position: static;
      width: 100%;
    }

    &__bottom {
      flex-direction: column-reverse;
    }

    &__image {
      margin-bottom: 30px;
      border-radius: 16px;
      width: 100%;
      height: auto;
    }

    &__text {
      padding: 24px 20px;
      border-radius: 16px;

      &::before {
        display: none;
      }
    }

    & .title-small {
      margin-bottom: 20px;
    }
  }
}

.inner-page .about {
  margin-bottom: 140px;

  .about__wrapper {
    display: flex;
    gap: 10px;
  }

  .about__text {
    display: flex;
    flex-direction: column;
    background-color: var(--back-main);
    
    & p {
      margin-bottom: 25px;
    }
  }

  .button {
    margin-top: auto;
    margin-right: auto;
    width: 320px;
  }
}

@media (max-width: 1023px) {
  .inner-page .about {
    margin-bottom: 40px;
  }

  .inner-page .about__wrapper {
    flex-direction: column;
  }

  .inner-page .about .button {
    margin-top: 24px;
    margin-right: 0;
    width: 100%;
  }
}