/* glob  */

:root {
  --white: #ffffff;
  --black: #313131;
  --main: #74947a;
  --accent: #cca288;
  --accent-light: #ddcabe;
  --back-main: #eef3ee;
  --back-accent: #f3f2ee;
  --back-grad: linear-gradient(94.37deg, #73947A 0%, #9FBEA6 128.71%);
  --border: #636d624d;
  --border-accent: #73947a80;
}

body {
  position: relative;
  font-family: "NunitoSans", sans-serif;
  color: var(--black);
  font-size: 18px;
  line-height: 130%;
}

.main {
  overflow-x: hidden;
}

.inner-page {
  margin-bottom: auto;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
  max-width: 1600px;
}

.section-offset {
  margin: 20px auto;
  margin-bottom: 20px;
  width: 100%;
  max-width: 1880px;
}

.section-inner {
  padding: 120px 0;
}

.block-back-main {
  border-radius: 40px;
  background-color: var(--back-main);
}

.block-back-accent {
  border-radius: 40px;
  background-color: var(--back-accent);
}

.title {
  font-size: 70px;
  font-weight: 600;
  line-height: 120%;
  overflow-wrap: break-word;
}

.title-secondary {
  font-size: 50px;
  font-weight: 600;
  line-height: 120%;
}

.title-third {
  font-size: 36px;
  font-weight: 600;
  line-height: 120%;
}

.title-small {
  font-size: 24px;
  font-weight: 600;
  line-height: 120%;
}

.no-scroll {
  overflow-y: hidden;
}

.no-scroll::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.541);
  backdrop-filter: blur(10px);
  z-index: 2;
}

.button {
  padding: 6px;
  padding-left: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 24px;
  width: max-content;
  max-width: 100%;
  height: 54px;
  background-color: var(--main);
  color: var(--white);
  border-radius: 40px;
  font-size: 16px;
  font-weight: 600;
  transition: background-color 0.7s;
}

.button:hover {
  background-color: var(--accent);
  transition: background-color 0.7s;
}

.button span {
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background-color: var(--white);
  display: flex;
  justify-content: center;
  align-items: center;
}

.button span::before {
  content: "";
  width: 15px;
  height: 12px;
  mask-image: url("../assets/img/icons/stack.svg#arrow-right");
  mask-position: center;
  background-color: var(--main);
}

.button:hover span::before {
  background-color: var(--accent);
  transition: background-color 0.7s;
}

.button:not(:has(span)) {
  padding: 15px;
  justify-content: center;
}

.button--accent {
  background-color: var(--accent);
  transition: background-color 0.7s;
}

.button--accent:hover {
  background-color: var(--main);
  transition: background-color 0.7s;
}

.button--inverse {
  background-color: transparent;
  border: 1px solid var(--border-accent);
  color: var(--main);
  transition: background-color 0.7s, color 0.7s;
}

.button--inverse:hover {
  background: var(--main);
  color: var(--white);
  border: none;
  transition: background-color 0.7s, color 0.7s;
}

.button--more.hidden {
  display: none;
}

.button--more {
  display: block;
  margin: 0 auto;
  width: 320px;
  text-align: center;
}

.input {
  padding: 24px;
  width: 100%;
  height: 60px;
  font-size: 16px;
  background-color: transparent;
  border-radius: 50px;
  border: 1px solid var(--border);
}

.input::placeholder {
  font-size: 16px;
  color: var(--black);
  opacity: 0.5;
}

input[type="checkbox"] {
  margin-top: 5px;
  position: relative;
  appearance: none;
  width: 18px;
  min-width: 18px;
  height: 18px;
  border-radius: 4px;
  left: 0;
  top: 0;
  border: 1px solid var(--main);
  cursor: pointer;
}

input[type="checkbox"]:checked::before {
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  left: 2px;
  top: 2px;
  background-image: url("../assets/img/icons/stack.svg#checkbox-green");
}

.popup input[type="checkbox"] {
  border-color: var(--white);
}

input[type="radio"] {
  margin-top: 5px;
  position: relative;
  min-width: 16px;
  width: 16px;
  height: 16px;
  border: 1px solid var(--main);
  border-radius: 50%;
  appearance: none;
  -webkit-appearance: none;
}

input[type="radio"]:checked::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background: var(--main);
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.input--textarea {
  height: 160px;
  resize: none;
}

address {
  font-style: normal;
}

.form {
  display: flex;
  flex-wrap: wrap;
  gap: 20px 12px;

  &__label {
    position: relative;
    width: 49%;
  }

  &__label--agree {
    display: flex;
    gap: 12px;
    font-size: 14px;

    & a {
      text-decoration: underline;
    }
  }

  &__button {
    width: 100%;
  }

  .form__label--file {
    position: relative;
    z-index: 10;
    padding: 38px 0;
    text-align: center;
    border-radius: 20px;
    border: 1px dashed #636d624d;
    cursor: pointer;

    & span {
      margin-bottom: 15px;
      display: block;
      font-weight: 600;
    }

    & p {
      margin-bottom: 32px;
      opacity: 0.6;
    }
  }

  input[type=file] {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    position: absolute;
    z-index: -10;
  }
}

.select {
  padding: 20px 24px;
  width: 100%;
  border-radius: 50px;
  border: 1px solid var(--border);
  appearance: none;
  font-size: 16px;
  background-image: url("../assets/img/icons/stack.svg#down");
  background-repeat: no-repeat;
  background-position: right 20px center;
  background-size: 15px 12px;
}

.textarea {
  padding: 24px;
  width: 100%;
  height: 160px;
  border-radius: 20px;
  border: 1px solid var(--border);
  resize: none;
}

.slider-navigation {
  display: flex;
  align-items: center;
  gap: 10px;
}

.swiper-button-prev,
.swiper-button-next {
  margin: 0;
  position: static;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  top: auto;
  right: auto;
  left: auto;
  border-radius: 50%;
  background-color: var(--back-main);
  transition: background-color 0.7s;
}

.swiper-button-prev svg,
.swiper-button-next svg {
  width: auto;
  height: auto;
  fill: var(--main);
  transition: fill 0.7s;
}

.block-back-main .swiper-button-prev,
.block-back-main .swiper-button-next {
  background-color: var(--white);
}

.swiper-button-prev:hover,
.swiper-button-next:hover {
  background-color: var(--main);
  transition: background-color 0.7s;
}

.swiper-button-prev:hover svg,
.swiper-button-next:hover svg {
  fill: var(--white);
  transition: fill 0.7s;
}

.swiper-button-next::after,
.swiper-button-prev::after {
  display: none;
}

@-webkit-keyframes transformItem {
  0% {
    opacity: 0;
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes transformItem {
  0% {
    opacity: 0;
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

.transform-item {
  opacity: 0;
}

.animation-active.transform-item {
  opacity: 1;
  -webkit-animation: transformItem 0.4s forwards;
  animation: transformItem 0.4s forwards;
}

.animation-active.transform-item:nth-child(2n) {
  -webkit-animation: transformItem 0.45s forwards;
  animation: transformItem 0.45s forwards;
}

.animation-active.transform-item:nth-child(3n) {
  -webkit-animation: transformItem 0.55s forwards;
  animation: transformItem 0.55s forwards;
}

.animation-active.transform-item:nth-child(5n) {
  -webkit-animation: transformItem 0.65s forwards;
  animation: transformItem 0.65s forwards;
}

.animation-active.transform-item:nth-child(7n) {
  -webkit-animation: transformItem 0.75s forwards;
  animation: transformItem 0.75s forwards;
}

@-webkit-keyframes animationBtn {
  0% {
    left: -55px;
  }

  100% {
    left: calc(100% + 50px);
  }
}

@keyframes animationBtn {
  0% {
    left: -55px;
  }

  100% {
    left: calc(100% + 50px);
  }
}

::-webkit-scrollbar-track {
  background-color: var(--back-main);
}

::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background-color: var(--main);
}

::-webkit-scrollbar {
  border-radius: 8px;
  height: 6px;
  width: 6px;
}

summary::marker {
  content: '';
}

summary::-webkit-details-marker {
  display: none;
}

@media (max-width: 1250px) {
  .form__label {
    width: 47%;
  }
}

@media (max-width: 1023px) {
  body {
    font-size: 16px;
  }

  .main {
    padding-top: 62px;
  }

  .section-inner {
    padding: 50px 0;
  }

  .block-back-main,
  .block-back-accent {
    border-radius: 20px;
  }

  .title {
    font-size: 32px;
  }

  .title-secondary {
    font-size: 26px;
  }

  .title-third {
    font-size: 20px;
  }

  .title-small {
    font-size: 18px;
  }

  .button {
    padding: 4px 4px 4px 24px;
    justify-content: space-between;
    min-width: 100%;
    height: 52px;
  }

  .button span {
    width: 44px;
    height: 44px;
  }

  .button--more {
    width: 100%;
  }

  .input {
    padding: 21px;
    height: 52px;
    font-size: 14px;
  }

  .input::placeholder {
    font-size: 14px;
  }

  input[type="checkbox"] {
    width: 14px;
    min-width: 14px;
    height: 14px;
  }

  input[type="checkbox"]:checked::before {
    width: 10px;
    height: 10px;
    left: 1px;
    top: 1px;
    background-size: 10px 10px;
  }

  input[type="radio"] {
    min-width: 14px;
    width: 14px;
    height: 14px;
  }

  input[type="radio"]:checked::before {
    width: 8px;
    height: 8px;
  }

  .input--textarea {
    height: 150px;
    resize: none;
  }

  .select {
    padding: 18px 21px;
    font-size: 14px;
  }

  .textarea {
    padding: 20px;
    width: 100%;
    height: 160px;
    border-radius: 20px;
    border: 1px solid var(--border);
    resize: none;
  }

  .slider-navigation {
    display: none;
  }

  .form {
    gap: 8px;

    &__label {
      position: relative;
      width: 100%;
    }

    &__agree {
      font-size: 12px;
    }

    .form__label--file {
      padding: 24px 20px;
      text-align: left;

      & span {
        margin-bottom: 20px;
        padding-right: 70px;
      }

      & p {
        margin-bottom: 0;
      }

      .form__icon {
        position: absolute;
        top: 28px;
        right: 24px;
      }
    }
  }
}