// prices

.prices {
  &__title {
    margin-bottom: 40px;
  }

  &__wrapper {
    display: flex;
    gap: 43px;
  }

  &__content {
    flex-grow: 1;
  }

  &__list {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;

    &-item {
      padding: 20px 20px 20px 32px;
      display: flex;
      align-items: center;
      border-radius: 20px;
      border: 1px solid var(--border);
    }

    &-name {
      flex-grow: 1;
      font-size: 20px;
    }

    &-cost {
      width: 14%;
      font-size: 20px;
      color: var(--main);
    }

    &-button {
      width: 116px;
      height: 40px;
    }
  }

  &__link {
    margin-top: 32px;
    display: flex;
    align-items: center;
    gap: 12px;
    color: var(--main);
  }

  &__link:hover {
    opacity: 0.8;
  }

  &__bottom {
    margin-top: 50px;
    padding: 50px;
    display: flex;
    align-items: center;
    gap: 50px;
    border-radius: 32px;
    background-color: var(--back-main);

    & .title-small {
      margin-bottom: 20px;
    }

    & .pay__item {
      width: calc(20% - 10px);
      background-color: #73947a1a;

      & svg {
        fill: var(--main);
      }
    }
  }

  &__flex {
    padding-right: 50px;
    border-right: 1px dashed var(--border);
  }
}

.prices .tabs-mobile {
  width: 25%;
}

.tabs-mobile {
  & span {
    display: none;
  }
}

.prices__tabs {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.prices__tab {
  padding: 10px 10px 10px 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
  border-radius: 20px;
  background-color: var(--back-accent);
  transition: color 0.7s, background-color 0.7s;

  & span {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 56px;
    height: 56px;
    border-radius: 12px;
    background-color: var(--white);
  }

  &:hover {
    color: var(--white);
    background-color: var(--accent);
    transition: color 0.7s, background-color 0.7s;

    & span {
      color: var(--black);
    }
  }
}

.prices__tab.active {
  background-color: var(--accent-light);
}

.prices__tab.active:hover {
  color: var(--black);
}

.prices-page {
  margin-bottom: 110px;
}

.question-form {
  .question-form__title {
    margin-bottom: 35px;
    text-align: center;
  }

  .lead {
    margin: 0 auto 50px auto;
    max-width: 800px;
    text-align: center;
  }

  & form {
    margin: 0 auto;
    padding: 50px;
    max-width: 752px;
    border-radius: 32px;
    background-color: var(--white);
  }

  .form__wrapper {
    display: flex;
    flex-direction: column;
    gap: 12px;
    width: 100%;

    .form__label {
      width: 100%;
    }
  }
}

@media (max-width: 1023px) {
  .prices {
    &__title {
      margin-bottom: 20px;
    }

    &__wrapper {
      flex-direction: column;
      gap: 30px;
    }

    &__list {
      gap: 8px;

      &-item {
        padding: 24px;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 20px;
        border-radius: 16px;
      }

      &-name {
        width: 100%;
        ;
        font-size: 16px;
      }

      &-cost {
        width: 46%;
        font-size: 18px;
      }

      &-button {
        min-width: 120px;
        width: 120px;
        height: 36px;
      }
    }

    &__link {
      margin-top: 20px;
    }

    .tab-content {
      transform: scaleY(0);
      transform-origin: top center;
      opacity: 0;
      max-height: 0;
      overflow: hidden;
      transition: transform 1s,
        opacity 1s,
        max-height 1s;
    }

    .tab-content.active {
      transform: scaleY(1);
      opacity: 1;
      max-height: 1000px;
      transition: transform 1s,
        opacity 1s,
        max-height 1s;
    }

    &__bottom {
      margin-top: 50px;
      padding: 0;
      flex-direction: column;
      gap: 10px;
      border-radius: 0;
      background-color: transparent;

      & .title-small {
        margin-bottom: 20px;
      }

      & .pay__item {
        width: 100%;
      }
    }

    &__flex {
      padding: 24px 20px;
      width: 100%;
      border-right: none;
      border-radius: 16px;
      background-color: var(--back-main);

      & .button {
        max-width: 360px;
        width: 100%;
      }
    }

    &__pay {
      padding: 24px 20px;
      border-radius: 16px;
      background-color: var(--back-main);
    }
  }

  .prices__tabs {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: calc(100% + 10px);
    left: 0;
    right: 0;
    padding: 24px;
    width: 100%;
    flex-direction: column;
    gap: 16px;
    border-radius: 8px;
    background-color: var(--back-main);
    transition: opacity 0.7s;
  }

  .prices__tab {
    padding: 0;
    border-radius: 0;
    text-align: left;
    font-size: 16px;
    color: var(--black);
    background-color: transparent;

    & span {
      display: none;
    }

    &:hover {
      color: var(--main);
      background-color: transparent;
    }
  }

  .prices__tab.active {
    color: var(--black);
    background-color: transparent;
  }

  .prices .tabs-mobile {
    position: relative;
    width: 100%;
  }

  .prices .tabs-mobile span {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px;
    border-radius: 12px;
    font-size: 16px;
    background-color: var(--back-main);
    cursor: pointer;

    &::after {
      content: "";
      display: block;
      width: 14px;
      height: 7px;
      background: url("../assets/img/icons/stack.svg#down");
      transition: transform 0.7s;
    }
  }

  .prices .tabs-mobile.opened {
    & span {
      outline: 1px solid var(--border);
    }

    &::after {
      transform: rotate(180deg);
      transition: transform 0.7s;
    }

    & .tabs {
      padding: 24px;
      opacity: 1;
      visibility: visible;
      z-index: 2;
      transition: opacity 0.7s;
    }
  }

  .prices .prices__tab span {
    display: none;
  }

  .prices-page {
    margin-bottom: 70px;
  }

  .prices__form {
    .prices__title {
      text-align: left;
    }

    .lead {
      margin: 0 0 25px 0;
      max-width: 100%;
      text-align: left;
    }

    & form {
      margin: 0 auto;
      padding: 24px 20px;
      max-width: 100%;
      border-radius: 16px;
    }

    .form__wrapper {
      width: 100%;
    }
  }

  .question-form form {
    padding: 24px 20px;
  }

  .question-form .question-form__title {
    margin-bottom: 20px;
    text-align: left;
  }

  .question-form .lead {
    margin: 0 0 30px 0;
    max-width: 100%;
    text-align: left;
  }
}