.steps {
  &__wrapper {
    background: url("../assets/img/sections/back-steps.png") top 50% left 50% no-repeat;
  }

  &__title {
    margin-bottom: 35px;
    text-align: center;
  }

  & .lead {
    margin-bottom: 146px;
    text-align: center;
  }

  &__slider {
    height: 100%;
  }
}

@media (min-width: 1024px) {
  .steps .swiper-wrapper {
    display: flex;
    gap: 10px;
  }
}

.step {
  padding: 30px;
  width: calc(20% - 10px);
  height: auto;
  border-radius: 24px;
  background-color: var(--white);

  &__top {
    margin-bottom: 50px;
    display: flex;
    justify-content: space-between;
  }

  &__time {
    padding: 8px 18px;
    border-radius: 40px;
    border: 1px solid var(--border);
  }

  &__number {
    padding: 8px 20px;
    border-radius: 40px;
    color: var(--white);
    background-color: var(--main);
  }

  &__title {
    margin-bottom: 20px;
    width: 80%;
  }
}

@media (max-width: 1440px) {
  .steps {
    &__wrapper {
      background: none;
    }

    & .swiper-wrapper {
      flex-wrap: wrap;
    }
  }

  .step {
    width: calc(33% - 10px);
  }
}

@media (max-width: 1023px) {
  .steps {

    &__title {
      margin-bottom: 20px;
      text-align: left;
    }

    & .lead {
      margin-bottom: 30px;
      text-align: left;
    }

    & .swiper-wrapper {
      flex-wrap: nowrap;
    }
  }

  .step {
    padding: 20px;
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 238px;
    border-radius: 16px; 

    &__top {
      margin-bottom: auto;
      display: flex;
      justify-content: space-between;
    }

    &__title {
      margin-bottom: 15px;
      width: 100%;
    }
  }
}