// faq

.faq {
  &__wrapper {
    position: relative;
  }

  &__title {
    margin-bottom: 35px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__link {
    position: absolute;
    top: 0;
    right: 0;
    width: 320px;
  }
}

.details {
  display: flex;
  flex-direction: column;
  border-radius: 20px;
  transition: 0.3s;
  border: 1px solid var(--border);
  transition: all 0.7s;

  &__title {
    padding: 30px;
    border-radius: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 20px;
    font-weight: 600;
    cursor: pointer;
  }

  &__content {
    margin-bottom: -80px;
    padding: 0 30px 30px 30px;
  }

  &[open] {
    padding-bottom: 80px;
    background-color: var(--back-main);
    border: none;
    transition: all 0.7s;
  }
}

.details__open {
  position: relative;
  width: 16px;
  height: 16px;
}

.details__open::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(0-50%);
  width: 16px;
  height: 2px;
  border-radius: 28px;
  background-color: var(--main);
  transition: opacity 0.3s;
}

.details__open::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  width: 2px;
  height: 16px;
  border-radius: 2px;
  background-color: var(--main);
}

details[open] .details__open::after {
  opacity: 0;
  transition: opacity 0.3s;
}

.faq__content {
  margin: 50px 0 140px 0;
}

@media (max-width: 1023px) {
  .faq__title {
    margin-bottom: 20px;
  }

  .faq__link {
    position: static;
    width: 100%;
  }

  .faq__list {
    margin-bottom: 30px;
  }

  .details {
    &__title {
      padding: 20px;
      font-size: 16px;
      gap: 10px;
    }

    &__content {
      padding: 0 20px 20px 20px;
    }
  }

  .details__open {
    min-width: 12px;
    width: 12px;
    height: 12px;
  }

  .details__open::before {
    width: 12px;
  }

  .details__open::after {
    height: 12px;
  }
}