// promos

.promos {
  &__wrapper {
    position: relative;
  }

  &__title {
    margin-bottom: 30px;
  }

  &__buttons {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    gap: 30px;
  }

  &__link {
    width: 320px;
  }
}

.promos__slider {
  overflow: hidden;
}

.promo {
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 32px;
  height: auto;
  border-radius: 32px;
  background-color: var(--white);
}

.promo__img {
  position: relative;
  width: 100%;
  height: 260px;
  border-radius: 26px;
  overflow: hidden;
}

.promo picture {
  width: 100%;
  height: 100%;
}

.promo img {
  position: relative;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.promo__wrapper {
  position: absolute;
  left: 16px;
  bottom: 16px;
  display: flex;
  gap: 6px;
}

.promo__bage {
  padding: 16px 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 40px;
  color: var(--white);
  background-color: var(--accent);
}

.promo__date {
  padding: 16px 18px;
  border-radius: 40px;
  background-color: var(--accent-light);
}

.promo:hover {
  outline: 1px solid var(--main);
  outline-offset: -1px;
  transition: outline 0.7s;
}

.promo__text {
  padding: 0 14px 14px 14px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.promo__name {
  margin-bottom: 20px;
}

.promo__description {
  margin-bottom: 24px;
}

.promo__button {
  margin-top: auto;
  width: 100%;
}

.promos-page {
  margin-bottom: 140px;
}

.promos__list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.promos__list .promo {
  width: calc(33% - 10px);
  background-color: var(--back-main);
}

@media (max-width: 1023px) {
  .promo {
    width: calc(50% - 10px);
  }
}

@media (max-width: 768px) {
  .promos {
    &__title {
      margin-bottom: 20px;
    }

    &__buttons {
      position: static;
    }

    &__link {
      width: 100%;
    }
  }

  .promos__slider {
    margin-bottom: 30px;
  }

  .promo {
    padding: 10px 10px 20px 10px;
    border-radius: 16px;

    &__img {
      height: 180px;
      border-radius: 14px;
    }
  }

  .promo__wrapper {
    left: 10px;
    bottom: 10px;
  }

  .promo__bage {
    padding: 10px 16px;
    font-size: 14px;
    line-height: 100%;
  }

  .promo__date {
    padding: 10px 16px;
    font-size: 14px;
    line-height: 100%;
  }

  .promo__text {
    padding: 0 10px;
  }

  .promo__name {
    margin-bottom: 16px;
  }

  .promo__description {
    margin-bottom: 24px;
  }

  .promos-page .promos__list {
    flex-direction: column;
    gap: 10px;
  }

  .promos__list .promo {
    width: 100%;
  }
}